import {login, logout} from '../../api/login'
import Cookies from 'js-cookie'
import hui from 'h_ui/dist/h_ui.min.js'

const user = {
  state: {
    user: '',
    token: Cookies.get('Admin-Token'),
    optAuths: []
  },
  mutations: {
    LOGIN_SUCCESS: () => {
    },
    LOGOUT_USER: state => {
      state.user = ''
    },
    SET_TOKEN: (state, token) => {
      state.token = token
    },
    SET_OPT_AUTHS: (state, optAuths) => {
      state.optAuths = optAuths
    }
  },
  actions: {
    // 登录
    // 将token信息保存
    Login ({commit}, userInfo) {
      return new Promise((resolve, reject) => {
         let obj = {}

         obj.authUsername = userInfo.username.trim()
         obj.authPassword = userInfo.password
         login(obj.authUsername,obj.authPassword).then(res => {
            if(res.data.success){
              var data = res.data.data
              Cookies.set('Admin-Token', data.operatorNo)
              Cookies.remove('lock')
              commit('SET_TOKEN', data.operatorNo)
              Cookies.set('OperatorNo', data.operatorNo)
              // 因为操作员中心，需要将该信息保存
              Cookies.set('operator_code', data.operatorNo)
              Cookies.set('OperatorName', data.operatorName)
              Cookies.set('CurrSysDate', data.currSysDate)
              Cookies.set('token', data.token)
              Cookies.set('user_token', data.token)
              resolve(data)
            }else{
              reject(res.data.msg)
            }
        }).catch(error => {
          reject(error)
        })
      })
    },
    // 登出
    // 将浏览器保存的token及权限清除
    Logout ({commit, state}) {
      let that = this
      return new Promise((resolve, reject) => {
          logout().then(res => {
              this.dispatch('ForceLogout').then(resolve)
          }).catch(error => {
              reject(error)
          })
      })
    },
    ForceLogout({commit, state}) {
      return new Promise((resolve, reject) => {
        commit('SET_TOKEN', '')
        Cookies.remove('Admin-Token')
        Cookies.remove('lock')
        Cookies.remove('menus');
        window.sessionStorage.removeItem('cop-menus')
        // Cookies.remove('menusType')
        Cookies.remove('OperatorName')
        Cookies.remove('OperatorNo')
        Cookies.remove('OperatorCode')
        Cookies.remove('CurrSysDate')
        Cookies.remove('token')
        Cookies.remove('companyId')
        resolve()
      })
    },

    ReLogin ({commit, state}) {
      commit('SET_TOKEN', '')
      Cookies.remove('Admin-Token')
      Cookies.remove('lock')
      Cookies.remove('menus');
      window.sessionStorage.removeItem('cop-menus');
      // Cookies.remove('menusType')
      Cookies.remove('OperatorName')
      Cookies.remove('OperatorNo')
      Cookies.remove('OperatorCode')
      Cookies.remove('CurrSysDate')
      Cookies.remove('companyId')
      location.reload()
    }
  }
}
export default user


