// The Vue build version to load with the `import` command
// (runtime-only or standalone) has been set in webpack.base.conf with an alias.
import Vue from 'vue'
import Url from 'url'
import App from './App'
import router from './router'
import store from './store'
import hui from 'h_ui'
import ElementUI from 'element-ui'
import './mixins'
// import './style/icon/iconfont.css'   //O45图标库
import 'h_ui/dist/h_ui.min.css'
import 'element-ui/lib/theme-chalk/index.css'
import './style/less/app.less'
import './utils/SSObservable'
import themed from './directive/themed'
import colored from './directive/colored'
import attridel from './directive/attridel'

//支持导出excel
import XLSX from 'xlsx'
import HFileExport from 'h_ui/dist/lib/FileExport.js'
Vue.component('h-file-export', HFileExport)

import { getMenuTree, getCurrSysDate } from './api/login.js'
import Cookies from 'js-cookie'
import * as filters from './filters'
import { parseUrl } from './utils/func'
import { getSkin } from './api/utils'
import AlertMsgBox from './components/AlertMsgBox'

window.store = store
Object.keys(filters).forEach((key) => {
  Vue.filter(key, filters[key])
})

Vue.prototype.LOCAL_CONFIG = window.LOCAL_CONFIG

Vue.prototype.openMask = function () {
  if (window.LOCAL_CONFIG.EMBEDDED) {
    let messageData = {
      type: 'openMask',
      value: true
    }
    window.parent.postMessage(messageData, '*')
  }
}
Vue.prototype.closeMask = function () {
  if (window.LOCAL_CONFIG.EMBEDDED) {
    let messageData = {
      type: 'openMask',
      value: false
    }
    window.parent.postMessage(messageData, '*')
  }
}

Vue.config.productionTip = false

Vue.use(hui)
Vue.use(ElementUI)
Vue.use(themed)
Vue.use(colored)
Vue.use(attridel)
Vue.$alert = Vue.prototype.$alert = AlertMsgBox
const whiteList = ['/login']

Vue.config.debug = true
/* 页面刷新前进行权限校验 */
router.beforeEach((to, from, next) => {
/* 开启页面加载进度提示 */
  hui.hLoadingBar.start()
  if (window.LOCAL_CONFIG.EMBEDDED) {
    let menus = JSON.parse(window.sessionStorage.getItem('cop-menus'))
    if (menus && menus.length > 0 && store.getters.addRouters.length > 0) {
      if (to.matched.length === 0) {
        from.path ? next({ path: from.path }) : next({ path: '/' })
      } else {
        next()
      }
    } else if (menus && menus.length > 0 && store.getters.addRouters.length == 0) {
      store.dispatch('GenerateMenuByMenus', menus)
      store.dispatch('GenerateRoutesByMenus', menus).then(() => {
        router.addRoutes(store.getters.addRouters)
        // 路由未定义
        next({ ...to })
      })
      getCurrSysDate().then(res => {
        if (res && res.data) {
          Cookies.set('CurrSysDate', res.data.data)
        }
      })
    } else {
      getMenuTree(store.getters.token).then(res => {
        if (res && res.data && res.data.length > 0) {
          // 根据权限生成对应菜单
          store.dispatch('GenerateMenuByMenus', res.data)
          // 根据权限生成路由
          store.dispatch('GenerateRoutesByMenus', res.data).then(() => {
            router.addRoutes(store.getters.addRouters)
            // 路由未定义
            next({ ...to })
          })
        } else {
        }
      })
      getCurrSysDate().then(res => {
        if (res && res.data) {
          Cookies.set('CurrSysDate', res.data.data)
        }
      })
    }
  } else
  if (store.getters.token) { // 有token，证明已登录
    if (to.path === '/login') {
      next({ path: '/' })
    } else {
      let menus = JSON.parse(window.sessionStorage.getItem('cop-menus'))
      if (menus && menus.length > 0 && store.getters.addRouters.length > 0) {
        if (to.matched.length === 0) {
          from.path ? next({ path: from.path }) : next({ path: '/' })
        } else {
          next()
        }
      } else if (menus && menus.length > 0 && store.getters.addRouters.length == 0) {
        store.dispatch('GenerateMenuByMenus', menus)
        store.dispatch('GenerateRoutesByMenus', menus).then(() => {
          router.addRoutes(store.getters.addRouters)
          // 路由未定义
          next({ ...to })
        })
      } else {
        getMenuTree(store.getters.token).then(res => {
          if (res && res.data && res.data.length > 0) {
            // 根据权限生成对应菜单
            store.dispatch('GenerateMenuByMenus', res.data)
            // 根据权限生成路由
            store.dispatch('GenerateRoutesByMenus', res.data).then(() => {
              router.addRoutes(store.getters.addRouters)
              // 路由未定义
              next({ ...to })
            })
          } else {
            /* ElementUI.MessageBox.alert('没有菜单权限', '提示', {
              confirmButtonText: '确定',
              type: 'info'
            });
            hui.hLoadingBar.finish(); */
          }
        })
        getSkin().then(res => {
          if(res.data && res.data.data){
            store.commit('SET_THEME', res.data.data)
          }
        })
      }
    }
  } else {
    if (whiteList.indexOf(to.path) !== -1) {
      next()
    } else {
      next('./login')
      hui.hLoadingBar.finish()
    }
  }
})
router.afterEach(() => {
  hui.hLoadingBar.finish()
})

/* eslint-disable no-new */
window.colorChange = (skin) => {
    store.commit('SET_THEME', skin)
}

/* eslint-disable no-new */
window.EventBus = new Vue();

window.onresize = () => {
  SSObservable.publish('onresize')
  SSObservable.publish('re_layout')
}

/**
 * 逻辑：
 * 设置为存储在本地的主题
 * 如果url存在主题，则设置为该主题，否则从接口中获取历史主题并设置为该主题
 */
store.commit('SET_THEME', store.state.theme.theme)
let params = Url.parse(parseUrl(location.href), true).query
if (params.skin) {
    store.commit('SET_THEME', params.skin.toLowerCase())
} else {
    if (store.getters.token) {
        getSkin().then(res => {
            if(res.data && res.data.data){
            store.commit('SET_THEME', res.data.data)
            }
        })
    }
}
new Vue({
  el: '#app',
  router,
  store,
  template: '<App/>',
  components: { App }
})



