<template>
  <div class="page page-login">
    <div class="login-container">
      <div class="login-img">
        <img src="../../assets/login.png">
        <h2>成就所托，持之以恒!</h2>
        <h3>洞悉需求，应势而生；风雨同舟，一路同行</h3>
      </div>
      <div class="login-box">
        <div class="login-header">
          <h3>{{sysName}}</h3>
        </div>
        <div class="login-content">
          <h-form autoComplete="on" :model="loginForm" ref="loginForm" label-position="left" class="card-box login-form" :rules="loginRules">
            <h4>用户登录<h-icon name="mine" colcor="red"></h-icon></h4>
            <h-form-item prop="username">
              <h-input name="username" type="text"  v-model="loginForm.username" autocomplete="on" placeholder="用户名" ref='username'>
                <h-icon name="mine" slot="prepend"></h-icon>
              </h-input>
              <div class="verify-tip verify-bottom" v-show='userError'>
                <div class="verify-tip-arrow"></div>
                <div class="verify-tip-inner">{{userErrorInfo}}</div>
              </div>
            </h-form-item>
            <h-form-item prop="password">
              <h-input name="password" type="password" @keyup.enter.native="handleLogin" v-model="loginPwd" autocomplete="on"
                placeholder="密码" ref='password' v-attrdel:title>
                <h-icon name="lock" slot="prepend"></h-icon>
              </h-input>
              <div class="verify-tip verify-bottom"  v-show='passError'>
                <div class="verify-tip-arrow"></div>
                <div class="verify-tip-inner">{{passErrorInfo}}</div>
              </div>
            </h-form-item>
            <h-form-item style="margin-bottom: 0">
              <h-button type="primary" style="width:100%;" :loading="loading" @click.native.prevent="handleLogin">
                登录
              </h-button>
            </h-form-item>
          </h-form>
        </div>
      </div>
      <div class="login-footer">@2017 恒生电子股份有限公司</div>
    </div>
  </div>
</template>

<script>
import fetch from '../../api/httpFetch.js'
import Cookies from 'js-cookie'
import {getKeys,encrypt} from  '@/api/utils'

  export default {
    name: 'login',
    data () {
      const validatePass = (rule, value, callback) => {
         if (this.capsLockOn) {
            callback(new Error('大写锁开启'))
          } else {
            callback()
          }
        // if (value.length < 6) {
        //   callback(new Error('密码不能小于6位'))
        // } else {
        //   callback()
        // }
      }
      return {
        loginForm: {
          username: '',
          password: '',
          checkcode: ''
        },
        keys:[],
        randomCode:'',
        loginRules: {
          username: [
            {
              required: false,
              trigger: 'change',
              message: '请填写用户名',
            }
          ],
          password: [
            {
              required: false,
              trigger: 'change',
              validator: validatePass
            }
          ],
          checkcode: [
            {
              required: true,
              trigger: 'blur'
            }
          ]
        },
        loading: false,
        showDialog: false,
        userError: false,
        userErrorInfo: '',
        codeError:false,
        codeErrorInfo: '',
        passError: false,
        passErrorInfo: '',
        sysName: this.LOCAL_CONFIG.SYS_NAME,
        capsLockOn: false
      }
    },
    watch: {
      loginForm: {
        handler: function () {
          this.userError = false
          this.passError = false
          this.loading = false
          this.codeError = false
        },
        deep: true
      }
    },
      computed: {
          loginPwd: {
              get: function () {
                  return ""
              },
              set: function (newValue) {
                  this.loginForm.password = encrypt(newValue,this.keys);
              }
          }
      },
    methods: {
      handleLogin () {
        if (this.keys.length === 0) {
          this.$alert("调用服务异常，请检查服务状态，并刷新后重新登录!", '错误', {type: 'error'});
          return;
        }
        this.$refs.loginForm.validate((valid) => {
          if (valid) {
            this.loading = true
              if (!this.loginForm.password){
                  this.$alert("登录密码不能为空,请填写后重新登录!", '错误', {
                    confirmButtonText: '确定',
                    type: 'error',
                    callback: action => {},
                    duration: 2000
                  }).then(() => {
                    console.log('!!!!!')
                  });
                  this.loading = false;
                  return
              }
              this.$store.dispatch('Login', this.loginForm).then((res) => {
                  this.$store.dispatch('unlockscreen')
                  this.loading = false
                  if (!res.success || res.userToken != "") {
                      this.$router.push({ path: '/' }).catch(e => {})
                  }  else {
                      this.$alert(res.errorInfo, '错误', {
                        confirmButtonText: '确定',
                        type: 'error',
                        callback: action => {}
                      });
                  }
            }).catch(err => {
                  this.$alert(err, '错误', {
                    confirmButtonText: '确定',
                    type: 'error',
                    callback: action => {}
                  });
                  this.loading = false;
            })
          } else {
            return false
          }
        })
      },
      refreshCode () {
        this.createCode()
      },
      codeBlur () {
        var checkCodeToken = this.randomCode;
        if(this.loginForm.checkcode==""){
            this.codeErrorInfo = "验证码不能为空";
            this.codeError = true;
        }
        if(this.loginForm.checkcode!=checkCodeToken){
            this.codeErrorInfo = "验证码不正确";
            this.codeError = true;
        }
      },
      nameBlur () {
        if(this.loginForm.username==""){
          this.userErrorInfo = "用户名不能为空";
          this.userError = true;
        }else{
        }
      },
      pwdBlur () {
        if(this.loginForm.password==""){
          this.passErrorInfo = "密码不能为空";
          this.passError = true;
        }else{

        }
      },
      /*随机干扰线条函数*/
      drawline(canvas, context) {
        //若省略beginPath，则每点击一次验证码会累积干扰线的条数
        context.beginPath();
        //起点与终点在canvas宽高内随机
        context.moveTo(Math.floor(Math.random() * canvas.width), Math.floor(Math.random() * canvas.height));
        context.lineTo(Math.floor(Math.random() * canvas.width), Math.floor(Math.random() * canvas.height));
        context.lineWidth = 1;
        context.strokeStyle = '#275DB3';
        context.stroke();
      },
    /*生成验证码*/
      createCode(){
        fetch.get('/getCheckCode').then(res=>{
          if(res && res.data){
            let data = res.data
            //每次生成code先将其清空防止叠加
            var canvas = document.getElementById("codeimg");
            var context = canvas.getContext("2d");
            context.clearRect(0, 0, canvas.width, canvas.height);
            context.strokeStyle = "#FFF";
            context.strokeRect(0, 0, canvas.width, canvas.height);
            var ranCode = data.code;
            if(ranCode=="") return;
            this.randomCode = ranCode;
            Cookies.set('checkCode-token', data.sessionId)
            //生成干扰线，数量随意
            for (var i = 0; i < 20; i++) {
                this.drawline(canvas, context);
            }
            //循环生成4位验证码
            for (var k = 0; k < ranCode.length; k++) {
              context.font='76px Arial';
              //将初始状态保存
              context.save();
              //获得-1到1的随机数
              var rA = 1-Math.random()*2;
              //获取随机倾斜角
              var angle = rA / 8 ;
              var ranNum = ranCode.charAt(k);
              //旋转生成的随机字符
              context.rotate(angle);
              //把rand()生成的随机数文本依次填充到canvas中，注意x坐标
              context.fillText(ranNum,20+45*k,100);
              //恢复初始状态，以便下一次循环
              context.restore();
            }
          }
        })
      }
    },
    created () {
      // document.getElementsByTagName('html')[0].className = 'theme-black'
      // document.body.className = 'login'
    },
    beforeDestroy() {
    },
    mounted () {
        getKeys().then(res=>this.keys =res.data);
        document.onkeydown = (event) => {
          if (event.code) {
            this.capsLockOn = event.getModifierState('CapsLock')
            this.$refs.loginForm.validateField('password')
          }
        }
      // this.createCode()
    },
    destroyed () {
      document.body.className = ''
      document.onkeydown = null
    }
  }
</script>

<style lang="less">
  @import "./style.less";
</style>


