<template>
  <div class="c-sidebar" v-show="childRoute.length > 0">
    <!-- 搜索 -->
    <div class="c-sidebar-head" :style="{width: isSidebarExpand ? '200px': '40px'}">
      <div class="c-sidebar-head-search" v-show="isSidebarExpand">
        <h-input type="text" style="width: 140px;" v-model="searchText" icon="close" placeholder="请输入关键词"
          @on-click="searchText = ''"></h-input>
      </div>
      <div class="expandIcon" @click.prevent.stop="expandSiderbar">
        <h-icon :name="isSidebarExpand ? 'return' : 'enter'"></h-icon>
      </div>
    </div>
    <!-- 菜单导航 -->
    <div class="c-sidebar-menu" :style="menuStyles">
      <h-menu
        :class="{'hideSidebar': !isSidebarExpand}"
        :shrinkClose="true"
        :active-name="activeName"
        :open-names="openNames"
        :collapse="!sidebar.opened"
        mode='vertical'
        theme='dark'
        width='220px'
        @on-select="onSelect"
        ref="menu">
        <template v-for="item in filteredRoutes">
          <h-submenu :name="item.title" v-if="!item.hidden && item.children.length > 0" :key="item.menu_id">
            <template slot="title">
              <!-- <h-icon :name="item.icon || 'manage'"></h-icon> -->
              <i :class="item.menu_icon_name || 'iconfont icon-manage'"></i>
              <span>{{ item.title }}</span>
            </template>
            <template v-for="child in item.children">
              <div v-if="!child.hidden" :key="child.menu_id">
                <h-menu-item :name="child.title" @click.native="showView(child)">
                  <!-- <h-icon :name="child.icon || 'ios-circle-outline'"></h-icon> -->
                  <i :class="child.menu_icon_name || 'iconfont icon-ios-circle-outline'"></i>
                  <span>{{ child.title }}</span>
                </h-menu-item>
              </div>
            </template>
          </h-submenu>
        </template>
      </h-menu>
    </div>
  </div>
</template>
<script>
  import { mapState, mapGetters } from 'vuex'
  import Cookies from 'js-cookie'
  import { deepCopy } from '../../utils/func'

  export default {
    data () {
      return {
        childRoute: [],
        filteredRoutes: [],
        openNames: [],
        activeName: '',
        search: '',
        searchText: '',
        notFoundText: '没有匹配页面',
        menusType: '0', // Cookies.get('menusType'),
        limit: this.LOCAL_CONFIG.TABS_VIEW_LIMIT
      }
    },
    computed: {
      ...mapGetters([
        'activeIndex',
        'menusRoot',
        'menusNoRoot',
        'searchRoute',
        'sidebar'
      ]),
      ...mapState({
        visitedViews: state => state.app.visitedViews
      }),
      isSidebarExpand () {
        return this.sidebar.opened
      },
      menuStyles() {
        if (this.isSidebarExpand) {
          return {
            overflowY: 'auto',
            overflowX: 'hidden'
          }
        }
        return ''
      }
    },
    watch: {
      activeIndex: {
        handler: function (val) {
          this.computChildRoute()
        }
      },
      childRoute: {
        handler: function (val) {
          this.filteredRoutes = val
        }
      },
      searchText: {
        handler: function (val) {
          if (val.length > 0) {
            this.filteredRoutes = this.filterRoutesByKeyword(this.childRoute, val)
            this.$nextTick(() => {
              this.openNames = this.filteredRoutes.filter(e => !e.hidden).map(e => e.title)
            })
          } else {
            this.filteredRoutes = deepCopy(this.childRoute)
          }
          this.$nextTick(() => {
            this.$refs.menu.updateActiveName()
          })
        }
      }
    },
    created () {
      this.computChildRoute()
      // 兼容IE中路由无法跳转问题
      let that = this
      if ('-ms-scroll-limit' in document.documentElement.style && '-ms-ime-align' in document.documentElement.style) {
        // detect it's IE11
        window.addEventListener("hashchange", function(event) {
          var currentPath = window.location.hash.slice(1);
          if (that.$route.path !== currentPath) {
            that.$router.push(currentPath)
          }
        }, false)
      }
    },
    mounted() {
       SSObservable.subscribe('tabviewChange', (name) => {
         this.$nextTick(() => this.activeName = name)
      })
    },
    methods: {
      showView(item) {
        /** 如果在已经打开项内，则跳转打开 */
        for (let visitedView of this.visitedViews) {
            if (visitedView.name === item.title) {
                this.$router.push(item.url).catch(e => {})
                return
            }
        }
        if (this.visitedViews.length >= this.limit) {
          this.$hMessage.info('打开的菜单太多了，请先关闭暂时不用的菜单');
          return
        }
        let obj = {
          name: item.title,
          path: item.url.replace('#', ''),
          id: item.id
        }
        this.$store.dispatch('addVisitedViews', obj)
        this.$router.push(obj.path).catch(e => {})
      },
      handleSearch (path) {
        this.search = ''
        this.$refs.select.setQuery('')
        this.$nextTick(() => {
          setTimeout(() => {
            this.$router.push(path)
          }, 300)
        })
      },
      expandSiderbar () {
        this.$store.dispatch('ToggleSidebar')
        SSObservable.publish('ToggleSidebar', this.isSidebarExpand)
      },
      computChildRoute () {
        if(this.menusType && this.menusType == '1' && this.menusRoot.length > 0) { //多个子系统
          this.childRoute = this.menusRoot[this.activeIndex].children
        } else { // 单个系统
          this.childRoute = this.menusNoRoot
        }
      },
      onOpenChange(names) {
        this.openNames = names
      },
      filterRoutesByKeyword(routes, keyword) {
        let copyed = deepCopy(routes)
        /** 设置隐藏 */
        let filter = (datas) => {
          for (let data of datas) {
            if (data.title.indexOf(keyword) < 0) {
              data.hidden = true
            }
            if (data.children && data.children.length > 0) {
              filter(data.children)
            }
          }
        }
        filter(copyed)
        /** 如果子项有显示，则父项也显示 */
        let solveParentHidden = (datas) => {
          for (let data of datas) {
            if (data.children && data.children.length > 0) {
              let allHidden = true
              for (let child of data.children) {
                if (!child.hidden) {
                  allHidden = false
                  break
                }
              }
              if (!allHidden) {
                data.hidden = false
              }
              solveParentHidden(data.children)
            }
          }
        }
        solveParentHidden(copyed)
        return copyed
      },
      onSelect(name) {
        this.activeName = name
      },
    },
  }
</script>

<style lang="less" rel="stylesheet/less">
@import "~variable";
@import "~mixins";

.c-sidebar {
  &-head {
    .flex(row, space-between, center);
    flex-shrink: 0;
    height: 40px;
    padding: 0 8px 0 12px;
    z-index: 2;
    .c-sidebar-head-search {
      .h-input-wrapper {
        height: 26px;
      }
      .h-input-icon-normal + .h-input {
        border: 1px solid #999999;
      }
      .h-input-icon {
        color: #999999;
      }
      .expandIcon {
        color: black;
        background-color: transparent;
      }
    }
    /* 重置 input */
    .h-input {
      color: white;
      height: 26px;
      border-color: white;
    }
    .h-input:focus {
      outline: none;
      border: 1px solid white;
    }
    .h-input-icon {
      line-height: 28px;
      color: white;
    }
    .expandIcon {
      .flex(row, center, center);
      width: 40px;
      font-size: 20px;
      color: white;
      cursor: pointer;
    }
  }
  &-menu{
    flex-grow: 1;
    padding-top: 0px;
    .iconfont {
      font-size: 16px;
    }
  }

}

</style>



