let SSObservable = {
  subscribers: [],
  subscribe: function (name, callback) {
    this.subscribers.push({
      name,
      callback
    })
  },
  unsubscribe: function (name, callback) {
    let len = this.subscribers.length
    for (let i = 0; i < len; i++) {
      let subscribe = this.subscribers[i]
      if (subscribe.name === name && subscribe.callback === callback) {
        this.subscribers.splice(i, 1)
        return
      }
    }
  },
  publish: function (name, data) {
    let finds = this.subscribers.filter(e => e.name === name)
    for (let subscribe of finds) {
      subscribe.callback(data)
    }
  }
}

window.SSObservable = SSObservable



