<template>
    <div class="c-module">
        <div class="ele-cop-tab" v-if="showSubMenu">
            <el-tabs v-model="subTabName" @tab-click="handleClick">
                <el-tab-pane v-for="(item,index) in tabList" :key="index" :label="item.menu_name" :name="item.menu_name">
                </el-tab-pane>
            </el-tabs>
        </div>
        <keep-alive>
            <router-view class="c-module-content" :style="{height: contentHeight + 'px'}"></router-view>
        </keep-alive>
    </div>
</template>

<script>
import { mapState, mapMutations } from 'vuex'
import { showTab, getMenuListByParent } from '@/api/utils'

export default {
  name: 'Module',
  data() {
    return {
      tabList: [],
      subTabName: '',
      contentHeight: 0,
      embedded: window.LOCAL_CONFIG.EMBEDDED,
      showSubMenu: window.LOCAL_CONFIG.SHOW_SUB_MENU
    }
  },
  computed: {
  },
  watch: { // 监听路由变化
    $route(to, from) {
      if ((to.path.split('/')[1] != from.path.split('/')[1] || (typeof to.path.split('/')[2] === 'undefined')) && to
        .path.split('/')[1] != 'mainIndex') {
        this.loadSubMenus()
      }
    }
  },
  created() {
    this.loadSubMenus()
  },
  mounted() {
    this.getContentHeight()
  },
  activated() {
    SSObservable.subscribe('onresize',this.getContentHeight)
  },
  deactivated() {
    SSObservable.unsubscribe('onresize',this.getContentHeight)
  },
  methods: {
    ...mapMutations({
      changeSubTabName: 'SET_SUBTAB_NAME',
      // addSubTabName: 'ADD_SUBTAB_NAME',
      delSubTabName: 'DEL_SUBTAB_NAME',
      addSubTabName: 'SET_SUBTAB_NAME_DICT'
    }),
    getContentHeight() {
      this.contentHeight = this.$el.clientHeight - (this.showSubMenu ? 60 : 10)
      SSObservable.publish('moduleHeightChange', this.contentHeight)
    },
    loadSubMenus() {
      getMenuListByParent(this.$route.path).then(res => {
        if (res && res.data && res.data.data.length > 0) {
          this.tabList = res.data.data
          let needInit = true
          this.tabList.forEach((x, index) => {
            x.menu_path = x.menu_path.replace('.htm', '')
            if (x.menu_path === this.$route.path) {
              //   this.tabName = x.menu_name
              needInit = false
            }
          })
          if (needInit) {
            // this.tabName = this.tabList[0].menu_name
            // this.$router.push(this.tabList[0].menu_path)
          }
        } else {
          this.tabList = []
        }

        if (this.showSubMenu && this.embedded) {
          // {"/reportmgnt":"银行流水查询","/systemmgnt":"审批规则配置"}
          let tabNameDict = this.$store.state.tabs.subTabNameDict
          let rootpath = this.getParentRoute()
          let subTabName = tabNameDict[rootpath]
          if (!subTabName) {
            subTabName = this.tabList[0].menu_name
          }
          this.subTabName = subTabName
          let find = null
          for (let item of this.tabList) {
            if (item.menu_name === this.subTabName) {
              find = item
              break
            }
          }
          console.log(find.menu_path)
          this.$router.push(find.menu_path)
        }
      }).catch(error => {
        console.log(error)
      })
    },
    handleClick(tab) {
      this.$router.push(this.tabList[tab.index].menu_path)
      let rootpath = this.getParentRoute()
      this.addSubTabName({
        rootpath: rootpath,
        subTabName: this.tabList[tab.index].menu_name
      })
    },
    getParentRoute() {
      return this.$route.matched[1].path
    }
  }
}

</script>

<style lang="less">
    .c-module {
        position: relative;
        width: 100%;
        height: 100%;
        overflow: hidden;
        background-color: #F7FAFB;
        .c-module-content {
            position: absolute;
            box-sizing: border-box;
            margin: 0 10px 10px 10px;
            padding: 10px;
            min-width: 800px;
            width: calc(~"100% - 20px");
            // height: calc(~"100% - 10px");
            background-color: white;
            border: 1px solid #e3ecf3;
        }
    }
</style>



