export function encrypt(pwd,keys) {
    if (!pwd||!keys||keys.length!=3)
        return ''
    return BizSecurity.DES.encrypt(pwd, keys[0], keys[1], keys[2])
}
export function decrypt(pwd,keys) {
    if (!pwd||!keys||keys.length!=3)
        return ''
    return BizSecurity.DES.decrypt(pwd, keys[0], keys[1], keys[2])
}

