export function parseDateStr (date,fmt){

    if (!date)
        return null
    if (typeof(date)=="string" ){
        return date.replace('-','').replace('-','')
    }
    if (!fmt)
        fmt="yyyyMMdd"
    var o = {
        "M+" : date.getMonth()+1,                 //月份
        "d+" : date.getDate(),                    //日
        "h+" : date.getHours(),                   //小时
        "m+" : date.getMinutes(),                 //分
        "s+" : date.getSeconds(),                 //秒
        "q+" : Math.floor((date.getMonth()+3)/3), //季度
        "S"  : date.getMilliseconds()             //毫秒
    };
    if(/(y+)/.test(fmt))
        fmt=fmt.replace(RegExp.$1, (date.getFullYear()+"").substr(4 - RegExp.$1.length));
    for(var k in o)
        if(new RegExp("("+ k +")").test(fmt))
            fmt = fmt.replace(RegExp.$1, (RegExp.$1.length==1) ? (o[k]) : (("00"+ o[k]).substr((""+ o[k]).length)));
    return fmt;
}

/**********
 * 获取月份最后一天
 * @param date
 * @returns {*}
 */
export function getMonthLastDay (date) {
    var flag = false;
    if (!date) {
        return null
    }
    var YearStr = date.substring(0,4);
    var MonthStr = date.substring(4,6);
    var dateStr ='';
    if( (YearStr%4 == 0 && YearStr%100 != 0) || YearStr%400 == 0 ){
        flag = true;
    }

    if(flag){
        if(MonthStr == '02'){
            dateStr = '29';
        }else if(MonthStr ==  '01' || MonthStr ==  '03' || MonthStr ==  '05' || MonthStr ==  '07' || MonthStr ==  '08' || MonthStr ==  '10' || MonthStr ==  '12'){
            dateStr = '31';
        }else{
            dateStr = '30';
        }
    }else{
        if(MonthStr == '02'){
            dateStr = '28';
        }else if(MonthStr ==  '01' || MonthStr ==  '03' || MonthStr ==  '05' || MonthStr ==  '07' || MonthStr ==  '08' || MonthStr ==  '10' || MonthStr ==  '12'){
            dateStr = '31';
        }else{
            dateStr = '30';
        }
    }
    return YearStr+MonthStr+dateStr;

}
//字符串yyyyMMddhhmmss转化为日期类型
export function parseStrDate(dateStr) {
    let date = new Date();
    date.setFullYear(dateStr.substring(0, 4), parseInt(dateStr.substring(4, 6)) - 1, dateStr.substring(6, 8));
    if (dateStr.length > 8) {
        date.setHours(dateStr.substr(8, 2), dateStr.substr(10, 2), dateStr.substr(12, 2), 0);
    }
    return date;
}

export function parseMinute(timeInt) {
    if (timeInt) {
        let timeStr = (Array(6).join(0) + timeInt).slice(-6)
        return '(' + timeStr.substring(0, 2) + ":" + timeStr.substring(2, 4) + '更新)'
    } else {
        return ''
    }
}

export function parseTime(timeInt) {
    if (timeInt || timeInt === 0) {
        let timeStr = (Array(6).join(0) + timeInt).slice(-6)
        return timeStr.substring(0, 2) + ":" + timeStr.substring(2, 4) + ":" + timeStr.substring(4, 6)
    } else {
        return ''
    }
}

export function parseDateString (dateInt,timeInt){
    let result = '';
    if (dateInt&&dateInt>0){
        let dateStr = dateInt.toString()
        result = result+ dateStr.substring(0,4)+'-'+dateStr.substring(4,6)+'-'+dateStr.substring(6,8);
    }
    if (timeInt&&timeInt>=0){
        let timeStr = (Array(6).join(0) + timeInt).slice(-6)
        result = result+" "+ timeStr.substring(0,2)+':'+timeStr.substring(2,4)+':'+timeStr.substring(4,6);
    }
    return result
}

export function showText(key, keyValueList, attributes) {
    if (!attributes){
        attributes={key:'code',value:'text'}
    }
    if (keyValueList){
        for (var i = 0; i < keyValueList.length; i++) {
            var row = keyValueList[i];
            if (key == row[attributes.key]) {
                return row[attributes.value]
            }
        }

    }
    return ''
}

export function showLabel(value, keyValueList, attributes) {
    if(!attributes) {
        attributes={key:'value',value:'label'}
    }

    if (keyValueList){
        for (var i = 0; i < keyValueList.length; i++) {
            var row = keyValueList[i];
            if (value == row[attributes.key]) {
                return row[attributes.value]
            }
        }
    }
    return ''
}

export function showTextList(value, keyValueList) {
    let label = ''
    if (value) {

        value.split(",").forEach(x => label = label + "，" + showText(x, keyValueList));
        label = label.substr(1, label.length)
    }
    return label
}
