/**
 * 宏定义处理
 *
 */
export  function resolveMacro(transferInfo, macroInfo, parttern,
                              businClassList,businFlagList,clearAgentList,settleMentList,depositTimeUnitList,selfAcct) {
    if (!parttern)
        return ''
    var month = parseInt(Cookies.get('CurrSysDate').substring(4, 6)) - 1;
    var season ;
    var place;
    if (month<3){
        season = '一季度';
    } else if (month<6){
        season = '二季度';
    } else if (month<9){
        season = '三季度';
    } else {
        season = '四季度';
    }
    var agentCode = transferInfo.clear_agent_code_cn;
    var bzClass = transferInfo.cop_busin_class;
    var bzFlag = transferInfo.cop_busin_flag;
    var settleMentName = showText(transferInfo.settle_ment, settleMentList);
    var settleType ;
    if (bzClass=="1"||bzClass=="3"){
        if (agentCode=='3'){
            place = 'ZZ';
        } else if (agentCode=='4') {
            place = 'SHCH';
        }
        settleType = settleMentName?settleMentName:'';
    }
    else if(bzClass=="4" || bzClass=="6")   /*上交所固收、上交所大宗*/
    {
        place="HZD";      /*上海中登*/
        settleType = "非担保";
    }
    else if(bzClass=="5")         /*深交所综合协议*/
    {
        place ="SZD";      /*深圳中登*/
        settleType = "非担保";
    }
    else if(bzClass=="9")     /*网下申购*/
    {
        settleType = "其他";
        if("10053,10098,20046,20050,20126,20127,20128,20139".indexOf(bzFlag)!=-1) {
            place = "HZD";   /*上海中登*/
        }
        else {
            place = "SZD";   /*深圳中登*/
        }
    }
    else
    {
        place="OTC";
        settleType = "其他";
    }

    var businFlagCaption = showText(transferInfo.cop_busin_flag, businFlagList);
    var businClassName = showText(transferInfo.cop_busin_class, businClassList);
    var agentCodeName = showText(transferInfo.clear_agent_code_cn, clearAgentList);
    var depositLimitTimeUnit = showText(transferInfo.deposit_limit_time_unit, depositTimeUnitList);
    var withdrawalDate = transferInfo.withdrawal_date;
    if(withdrawalDate == '0'){
        withdrawalDate ='';
    }

    let stockholderId;
    if(transferInfo.cop_busin_class == '4' || transferInfo.cop_busin_class == '6'){
        stockholderId = macroInfo.stockholder_id;
    }else if(transferInfo.cop_busin_class == '5'){
        stockholderId = macroInfo.stockholder_id_sz;
    }else{
        stockholderId = '';
    }

    let rivalName;
    if(transferInfo.rival_name){
        rivalName = transferInfo.rival_name;
    }else if(transferInfo.traderival_name){
        rivalName = transferInfo.traderival_name;
    }

    let balance = transferInfo.settle_balance;
    if(transferInfo.settle_balance && transferInfo.settle_balance.indexOf('.') == -1){
        balance = balance + '.00';
    }

    return parttern.replace('#1', macroInfo.fund_code)
        .replace('#2',balance ? balance : '')
        .replace('#3',Cookies.get('CurrSysDate').substring(0, 4) +'年')
        .replace('#4',season)
        .replace('#5',month+1+'月')
        .replace('#6',transferInfo.seat_id?transferInfo.seat_id:'')
        .replace('#7',stockholderId?stockholderId:'')
        .replace('#8',transferInfo.report_code?transferInfo.report_code:'')
        .replace('#9',businClassName?businClassName:'')
        .replace('#a',transferInfo.settle_amount?transferInfo.settle_amount:'')
        .replace('#b',transferInfo.require_pay_date?transferInfo.require_pay_date:'')
        .replace('#c',businFlagCaption?businFlagCaption:'')
        .replace('#d',transferInfo.deposit_limit_time?transferInfo.deposit_limit_time+depositLimitTimeUnit:'')
        .replace('#e',"")
        .replace('#f',agentCodeName?agentCodeName:'')
        .replace('#g',settleMentName?settleMentName:'')
        .replace('#h',macroInfo.sq_holder_account?macroInfo.sq_holder_account:'')
        .replace('#i',macroInfo.zz_holder_account?macroInfo.zz_holder_account:'')
        .replace('#j',macroInfo.fund_name?macroInfo.fund_name:'')
        .replace('#k',macroInfo.sh_ta_account?macroInfo.sh_ta_account:'')
        .replace('#l',macroInfo.sz_ta_account?macroInfo.sz_ta_account:'')
        .replace('#m',transferInfo.remark?transferInfo.remark:'')
        .replace('#n',transferInfo.apply_price?transferInfo.apply_price:'')
        .replace('#o',transferInfo.stock_name?transferInfo.stock_name:'')
        .replace('#p',withdrawalDate?withdrawalDate:'')
        .replace('#q',rivalName?rivalName:'')
        .replace('#r',place)
        .replace('#s',settleType)
        .replace('#t',transferInfo.deal_no?transferInfo.deal_no:'')
        .replace('#u',transferInfo.original_transfer_purpose?transferInfo.original_transfer_purpose:'')
        .replace('#v',transferInfo.zdsh_apply_no?transferInfo.zdsh_apply_no:'')
        .replace('#w',transferInfo.deal_no_end?transferInfo.deal_no_end:'')
        .replace('#x',transferInfo.deal_no_new?transferInfo.deal_no_new:'')
        .replace('#y', selfAcct && selfAcct.sh_fdb_account ? selfAcct.sh_fdb_account : '')
        .replace('#z', selfAcct && selfAcct.sz_fdb_account ? selfAcct.sz_fdb_account : '');
}


export function  showCity(provinceCity, cityTree) {
    let label = ''
    if (provinceCity && provinceCity.length > 1) {
        cityTree.forEach(x => {
            if (provinceCity[0] == x.value) {
                label = x.label
                x.children.forEach(y => {
                    if (provinceCity[1] == y.value) {
                        label = label + "/" + y.label
                    }
                })
            }
        })
    }

    return label
}


export function formatMoney(s) {
    if(s<1 &&s>0){
        s= '0'+s
    }
    let direction = 1;
    if (s < 0) {
        direction = -1;
        s = -s;

    }


    if (/[^0-9\.]/.test(s))
        return "N/A";

    if (s == null || s == "null" || s === "")
        return "N/A";

    s = s.toString().replace(/^(\d*)$/, "$1.");
    s = (s + "00").replace(/(\d*\.\d\d)\d*/, "$1");
    s = s.replace(".", ",");
    var re = /(\d)(\d{3},)/;
    while (re.test(s))
        s = s.replace(re, "$1,$2");
    s = s.replace(/,(\d\d)$/, ".$1");
    if (direction === -1) {
        s = '-' + s;
    }

    return s;
}

//金额排序函数
export function amountSortMethod(a, b, type) {
    let a4Float = parseFloat(a);
    let b4Float = parseFloat(b);
    if (type === 'asc') {
        if (a4Float < b4Float) {
            return -1;
        } else if (a4Float === b4Float) {
            return 0;
        } else {
            return 1;
        }
    } else if (type === 'desc') {
        if (a4Float > b4Float) {
            return -1;
        } else if (a4Float === b4Float) {
            return 0;
        } else {
            return 1;
        }
    }
}

