//过滤掉子指令
export function filterTransferChildren(selects, that) {
    selects = selects.filter(s => !(s.transfer_merge_flag === '0' &&
        s.transfer_ins_batch_no && parseInt(s.transfer_ins_batch_no) > 0));
    if (selects.length <= 0) {
        that.openMask();
        that.$alert('轧差后的子指令不能操作', '错误', {
            confirmButtonText: '确定',
            type: 'error',
            callback: action => {
                that.closeMask();
            }
        }).then(() => {
            that.closeMask();
        });
    }
    return selects;
}

//支持单选子指令，如果轧差指令及其子指令同时勾选，则以子指令为准
export function filterDuplicateTransfer(selects, that) {
    // 子指令
    let subTransfer = selects.filter(s => (s.transfer_merge_flag === '0' &&
        s.transfer_ins_batch_no && parseInt(s.transfer_ins_batch_no) > 0));
    let exclude = new Set()
    subTransfer.forEach(function (item,index,array){
        exclude.add(item.transfer_ins_batch_no)
    })
    // 父指令及普通指令
    let tempParentTransfer = selects.filter(s => !(s.transfer_merge_flag === '0' &&
        s.transfer_ins_batch_no && parseInt(s.transfer_ins_batch_no) > 0));
    let parentTransfer = tempParentTransfer.filter(s => !exclude.has(s.transfer_ins_id))
    let tempselects = []
    selects = tempselects.concat(subTransfer).concat(parentTransfer)
    return selects;
}



