<template>
  <div>
    <div @click="lockScreen">
      <h-icon name="lock"></h-icon>
    </div>

    <h-msg-box class="customMsgBox customMsgBox-lockscreen"  v-model = "isShow"
            title = "请输入解锁密码"
            :closable = "closable"
            transfer
            :mask-closable="false"
            width=300
            @keyup.enter.native="lockValid"
            ref = "msgbox">
      <input type="text" style="width: 140px; height: 30px;position: absolute;top:-1000px;"></input>
      <h-input placeholder="请输入密码" v-model="password" autocomplete="xxexx" type="password" v-attrdel:title></h-input>
      <div slot="footer">
          <h-button type="error" @click="lockValid">确定</h-button>
      </div>
    </h-msg-box>
  </div>
</template>
<script>
  import {post} from "@/api/bizApi/commonUtil";
  import {login} from '@/api/login'
  import {mapGetters, mapState} from 'vuex'
  import Cookies from 'js-cookie'
  import {getKeys,encrypt} from  '@/api/utils'
  export default {
    name: 'LockScreen',
    data () {
      return {
        password: '',
        disabled: true,
        errorTip: '',
        closable: false,
        isShow: false,
        timer: null,
        embedded:window.LOCAL_CONFIG.EMBEDDED
      }
    },
    watch:{
      isShow:function (show) {
        if(show){
          this.openMask();
        }else{
          this.closeMask();
        }
      }
    },
    props: {
      lockTime: {
        type: Number,
        default: 1
      }
    },
    computed : {
      ...mapGetters([
        'lock'
      ]),
      ...mapState({
        lock: function (state) {
          let sLockscreen = Cookies.get('lock')
          if (state.app.lock == '' && sLockscreen == 'true') {
            this.$store.dispatch('lockscreen')
          }
          return state.app.lock
        }
      }),
      lockTimes () {
        return this.lockTime * 60 * 1000
      }
    },
    methods: {
      lockTimer () {
        clearTimeout(this.timer)
        let that = this
        if (!this.isShow) {
        this.timer = setTimeout(function() {
          that.lockScreen()
        } , that.lockTimes)
        }
      },
      lockScreen () {
        this.$store.dispatch('lockscreen')
        this.isShow = true
      },
      setTip (tip) { // 消息提示，1.5秒后自动关闭
        this.errorTip = tip
        this.$hMessage.info(tip)
      },
      lockValid () {
        if (!this.password) {
          this.setTip('密码不能为空')
        } else {
          if (this.$store.getters.token) {
            let username = Cookies.get('OperatorNo');
            if (username) {
                let pwd =  encrypt(this.password,this.keys);
                login(username, pwd).then(res => {

                if(res.data.data){
                    this.isShow = false
                    this.password = ''
                    this.$store.dispatch('unlockscreen')
                }else{
                    this.$alert(res.data.msg,'错误',{
                        confirmButtonText: '确定',
                        type: 'error',
                        zIndex: '2000',
                        tag: 'some'
                    });
                    this.password = ''
                }
              }).catch(error => {
                  this.$alert(error,'错误',{
                      confirmButtonText: '确定',
                      type: 'error',
                      zIndex: '2000',
                      tag: 'some'
                  });
              })
            } else {
                this.$alert('当前未登录，系统出错!','错误',{
                    confirmButtonText: '确定',
                    type: 'error',
                    zIndex: '2000',
                    tag: 'some'
                });
                this.$store.dispatch('ReLogin')
            }
          }
        }
      }
    },
    created() {
      if(this.lock) {
        this.isShow = true
      }
    },
    mounted () {
        if (!this.embedded){
            document.addEventListener('mousemove', this.lockTimer)
            document.addEventListener('mousedown', this.lockTimer)
            getKeys().then(res=>this.keys =res.data);
        }

    },
    beforeDestory() {
        if (!this.embedded) {
            document.removeEventListener('mousemove', this.lockTimer)
            document.removeEventListener('mousedown', this.lockTimer)
        }
    }
  }
</script>

<style lang="less">
.customMsgBox-lockscreen {
  .h-modal-mask, .h-modal-wrap {
    z-index: 1009 !important;
  }
}
</style>


