<template>
	<div class="c-app app">
    <!-- 页面顶部 -->
    <div v-if="showTopBar" class="c-topbar-wrapper">
      <topbar></topbar>
    </div>
    <div class="c-app-content-wrapper"  v-bind:class="{ embedded: !showNav}">
      <!-- 侧边栏 -->
      <sidebar class="c-sidebar" :style="{width: isSidebarExpand ? '200px': '40px'}" v-if="showSideBar"></sidebar>
      <!-- 中间区域 -->
      <div class="c-app-content" >
        <navbar class="c-app-content-navbar" v-if="showNav"></navbar>
        <app-main class="c-app-main"></app-main>
      </div>
    </div>
  </div>
</template>

<script>
import { Navbar, Sidebar, AppMain, Topbar } from './index'
import { mapGetters } from 'vuex'
export default {
  name: 'layout',
  components: {
    Navbar,
    Sidebar,
    AppMain,
    Topbar
  },
  data() {
    return {
        showTopBar: !window.LOCAL_CONFIG.EMBEDDED,
        showSideBar: !window.LOCAL_CONFIG.EMBEDDED,
        showNav: !window.LOCAL_CONFIG.EMBEDDED
    }
  },
  computed: {
    sidebar () {
      return this.$store.state.app.sidebar
    },
    isSidebarExpand () {
      return this.sidebar.opened
    },
    ...mapGetters([
      'activeIndex'
    ])
  }
}
</script>

<style lang="less">
  @import "~mixins";
  .c-app {
    display: flex;
    flex-direction: column;
    .fill-all;
    .c-topbar-wrapper {
      height: 50px;
      flex-shrink: 0;
    }
    .c-app-content-wrapper {
      display: flex;
      flex-grow: 1;
      overflow: hidden;
      .c-sidebar {
        display: flex;
        flex-direction: column;
      }
      .c-app-content {
        position: relative;
        display: flex;
        flex-direction: column;
        flex-grow: 1;
        width: 100%;
        .c-app-content-navbar {
          overflow: hidden;
        }
        .c-app-main {
          position: relative;
          width: 100%;
          height: 100%;
          overflow-x: hidden;
          overflow-y: hidden;
          box-sizing: border-box;
        }
      }
    }
  }
</style>



