import Vue from 'vue'
import ContextMenu from './context-menu.vue'

let instance = null
const ContextMenuConstructor = Vue.extend(ContextMenu)
let SContextMenu = (option = {}) => {
  if (instance) {
    if (instance.$el.parentNode) {
      instance.$el.parentNode.removeChild(instance.$el)
    }
  }
  instance = new ContextMenuConstructor({
    el: document.createElement('div')
  })
  instance.pos = option.pos || { x: 0, y: 0 }
  instance.menus = option.menus || []
  instance.onMenuClick = option.onMenuClick || null
  instance.show = () => {
    instance && (instance.visible = true)
  }
  instance.remove = () => {
    instance && (instance.visible = false)
    instance && document.body.removeChild(instance.$el)
    instance = null
  }
  document.body.appendChild(instance.$el)
  return instance
}

export default SContextMenu



