<template>
  <section>
    <input type="text" style="width: 140px; height: 30px;position: absolute;top:-1000px;"></input>
    <input type="password" style="width: 140px; height: 30px;position: absolute;top:-1000px;" v-attrdel:title></input>
    <keep-alive>
      <router-view></router-view>
    </keep-alive>
  </section>
</template>

<script>
export default {
  name: 'AppMin',
  computed: {
    key() {
      return this.$route.name !== undefined ? this.$route.name + new Date() : this.$route + new Date()
    },
    sidebar() {
      return this.$store.state.app.sidebar
    },
    marginLeft() {
      if (!window.LOCAL_CONFIG.EMBEDDED) {
        return this.sidebar.opened ? '200px' : '40px'
      } else {
        return '10 px'
      }
    }
  }
}

</script>

<style lang="less"></style>



