// 删除input的某个属性
var plugin = function (Vue) {
    return Vue.directive('attrdel', {
        bind: function (el, binding, vnode, oldVnode) {
            let name = binding.arg;
            let inputDom = el.querySelector('input')
            if (name && inputDom) {
                inputDom.addEventListener('input', () => {
                    inputDom.removeAttribute(name);
                }, false);

                inputDom.addEventListener('mouseover', () => {
                    inputDom.removeAttribute(name);
                }, false);
            }
        },
        inserted: function (el) {
        }
    })
};

module.exports = plugin;



