<template xmlns:v-themed="http://www.w3.org/1999/xhtml">
  <div>
    <h-msgBox class="s-msg-box customMsgBox" :id="tag" :mask-closable="true" :top="200" :footerHide="isFooterHide" :canDrag="false" :title="title" cancel-text="" v-model="visible" ref="box">
      <div class="content">
        <div class="iconfont icon-prompt color-info" v-if="type === 'info'"></div>
        <div class="iconfont icon-success color-success" v-if="type === 'success'"></div>
        <div class="iconfont icon-warning color-warning" v-if="type === 'warning'"></div>
        <div class="iconfont icon-delete color-error" v-if="type === 'error'"></div>
        <div class="info" v-themed:text v-if="!multiline">{{content}}</div>
        <div class="info" v-else>
          <div v-for="val in content" v-themed:text>{{val}}</div>
        </div>
      </div>
    </h-msgBox>
  </div>
</template>

<script type="text/ecmascript-6">
export default {
  props: {
    value: {
      type: Boolean,
      default: false
    },
    duration: {
      type: Number,
      default: 0
    },
    type: {
      type: String,
      default: 'info' // success / info / warning / error
    },
    callback: {
      type: Function,
      default: null
    },
    multiline: {
      type: Boolean,
      default: false
    },
    zIndex: {
      type: String,
      default: '1000'
    },
    tag: {
      type: String,
      default: ''
    },
    title: String,
    content: [String, Array]
  },
  data() {
    return {
      visible: false,
      loading: false,
      timer: null
    }
  },
  computed: {
    isFooterHide() {
      return this.type === 'success'
    }
  },
  watch: {
    value: {
      handler: function (val) {
        this.visible = val
      },
      immediate: true
    },
    visible: {
      handler: function (val) {
        this.$emit('input', val)
        if (!val) {
          this.timer && clearTimeout(this.timer)
          this.timer = null
        } else {
          if (this.duration > 0) {
            if (this.callback) {
                this.callback()
              } else {
                this.resolve && this.resolve()
            }
            this.timer = setTimeout(() => {
              this.visible = false
            }, this.duration)
          }
        }
        if (!val) {
          setTimeout(() => {
            this.$el.parentElement.removeChild(this.$refs.box.$el)
          }, 300);
        }
      }
    },
    zIndex: {
      handler: function (val) {
        this.$nextTick(() => {
          if (this.tag && this.tag.length > 0) {
            let maskDom = document.body.querySelector(`#${this.tag}.s-msg-box .h-modal-mask`)
            let wrapDom = document.body.querySelector(`#${this.tag}.s-msg-box .h-modal-wrap`)
            if (maskDom && wrapDom) {
              maskDom.style.zIndex = val
              wrapDom.style.zIndex = val
            }
          }
        })
      }
    }
  },
  mounted() {

  },
  activated() {

  },
  methods: {

  },
  components: {

  }
}
</script>

<style lang="less" rel="stylesheet/less">
.s-msg-box {
  .content {
    display: flex;
    line-height: 20px;
    font-size: 13px;
    user-select: text;
    .iconfont {
      font-size: 26px;
      margin-right: 8px;
    }
    .green {
      color: green;
    }
    .color-info {
      color: #298DFF;
    }
    .color-success {
      color: #1ABE6B;
    }
    .color-warning {
      color: #FF9901;
    }
    .color-error {
      color: #F5222D;
    }
  }
  .h-modal-content {
    border: 1px solid #073ebe;
    border-radius: 0;
  }
  .h-modal-close {
    color: white;
    top: 5px;
    right: 10px;
  }
  .h-modal-header {
    padding: 5px 12px;
    background: #1388e6;
    .h-modal-header-inner {
      font-size: 12px;
      font-weight: normal;
      color: white;
    }
  }
  .h-modal-body {
    padding: 20px;
  }
  .h-modal-footer {
    padding: 10px 20px;
    border-top: 0;
    & > button:first-child {
      display: none;
    }
  }


}
</style>



