import fetch from './httpFetch.js'
import Cookies from 'js-cookie'
import Qs from 'qs'
let key1, key2, key3
require('../utils/BizSecurity.js')

// 登录-获取key
function getKeys () {
  return fetch.get('/getSecurityKey.json', {
      authUsername: '999800001',
      authPassword: '1'
  })
}

export function getCurrSysDate(){
    return fetch.get('/common/getCurrSysDate.json')
}

//登录
export function login (username,password) {
  return fetch.post('/loginCheck.json',{userCode:username,passWord:password},{
    headers: {
      'Content-Type': 'application/x-www-form-urlencoded'
    },
    transformRequest: function (req) {
      return Qs.stringify(req);
    }
  })
}
export function logout() {
    return fetch.post("/logout.json")
}



function convertTreeData(rows, attributes) { //把菜单转换为树形结构的方法
    var keyNodes = {},
        parentKeyNodes = {};
    for (var i = 0; i < rows.length; i++) {
        var row = rows[i];
        row.id = row[attributes.id];
        row.parentId = row[attributes.parentId];
        row.title = row[attributes.title] ? row[attributes.title] : null
        if (row[attributes.url]) {
            row[attributes.url] = row[attributes.url].replace(/\\/g, '')
        }
        row.url = row[attributes.url] ? row[attributes.url] : null
        row.icon = row[attributes.icon] ? row[attributes.url] : null
        row.children = [];

        keyNodes[row.id] = row;

        if (parentKeyNodes[row.parentId]) {
            parentKeyNodes[row.parentId].push(row);
        } else {
            parentKeyNodes[row.parentId] = [row];
        }

        var children = parentKeyNodes[row.id];
        if (children) { row.children = children; }

        var pNode = keyNodes[row.parentId];
        if (pNode) { pNode.children.push(row); }
    }
    return parentKeyNodes[attributes.rootParentId];
}

export function getMenuTree(token) { //获取菜单项请求
    return new Promise((resolve, reject) => {
      fetch.post('/common/getMenuList.json','').then(res => {
        if (res && res.data.data.length > 0) {
              const data = convertTreeData(res.data.data, {
                  id: 'menu_id',
                  title: 'menu_name',
                  parentId: 'menu_parent_id',
                  // icon: 'menu_icon',
                  url: 'menu_path',
                  rootParentId: '0'
              })
              resolve({ 'data': data })
        } else {
            resolve()
        }
      })
    })
}


//服务端校验是否登录
export function checkUserToken(token){
    return fetch.post('/common/webmenu/checkUserToken.json')
}

export function changePwd(oldPwd, newPwd) {
  let sessionId = Cookies.get('Admin-Token')
  return new Promise((resolve, reject) => {
    getKeys().then(res => {
      key1 = res.data[0]
      key2 = res.data[1]
      key3 = res.data[2]
      oldPwd = BizSecurity.DES.encrypt(oldPwd, key1, key2, key3)
      newPwd = BizSecurity.DES.encrypt(newPwd, key1, key2, key3)
      const data = {
        oldPwd,
        newPwd
      }
      resolve(fetch.post('/changePwd.json',data))
    })
  })
}

export function changeToPwd(oldPwd, newPwd){
    return fetch.post('/common/user/changeToPwd.json',{
      oldPwd: oldPwd,
      newPwd: newPwd
  })
}





