import Vue from 'vue'

Vue.mixin({
  mounted() {
  },
  activated() {
    if (this.re_layout || this.reLayout) {
      SSObservable.subscribe('re_layout', this.triggleRelayout)
      this.triggleRelayout()
    }
  },
  deactivated() {
    if (this.re_layout || this.reLayout) {
      SSObservable.unsubscribe('re_layout', this.triggleRelayout)
    }
  },
  methods: {
    triggleRelayout() {
      this.$nextTick(() => {
        if (this.reLayout) {
          this.reLayout()
        } else {
          this.re_layout && this.re_layout()
        }
      })
    },
    fixElPopoverBug() {
      let bugFix = () => {
        let els1 = document.body.querySelectorAll('.el-popper.el-cascader__dropdown')
        let els2 = document.body.querySelectorAll('.el-select-dropdown.el-popper')
        let els = [...els1, ...els2]
        for (let el of els) {
          el.style.display = 'none'
        }
      }
      let boxes = document.body.querySelectorAll('.customMsgBox')
      for (let box of boxes) {
        box.addEventListener('mousedown', bugFix)
      }
    }
  }
})



