<template>
  <div class="h-navbar-wrapper"  >
    <!-- 已打开标签 -->
    <tabs-view></tabs-view>
  </div>
</template>
<script>
  import { mapGetters } from 'vuex'
  // import Levelbar from './Levelbar'
  import TabsView from './TabsView2'
  export default {
    computed: {
      ...mapGetters([
        'sidebar'
      ]),
      isSidebarExpand () {
        return this.sidebar.opened
      },
    },
    components: {
      TabsView
    }
  }
</script>

<style lang="less">
</style>


