import {asyncRouterMap, constantRouterMap} from '../../router'
import Layout from '@/views/frame/Layout.vue'
import MainIndex from '@/views/mainIndex2.vue'
import Module from '@/views/Module.vue'

const permission = {
  state: {
    routers: constantRouterMap,
    addRouters: [],
    searchRoute: []
  },
  mutations: {
    SET_ROUTERS: (state, routers) => {
      state.routers = [...constantRouterMap.concat(routers)]
      state.addRouters = [...routers]
    },
    SEARCH_ROUTE: (state, data) => {
      state.searchRoute = data
    }
  },
  actions: {
    // 根据服务端菜单生成路由
    GenerateRoutesByMenus ({commit}, data) {
      return new Promise(resolve => {
        let accessedRouters = filterRouterByMenus([], data, [])
        let main = [{
          path: '/mainIndex',
          name: '我的工作台',
          component: MainIndex,
          hidden: true
        }]
        let allRouter = [...main.concat(accessedRouters)]
        // 初始化页面配置
          let route = [{
          path: '/',
          component: Layout,
          redirect: '/mainIndex',
          children: allRouter
        }]
        commit('SET_ROUTERS', route)
        commit('SEARCH_ROUTE', accessedRouters)
        resolve()
      })
    }
  }
}
export default permission

/**
 * 根据后台传回的可用menus,递归过滤异步路由表，返回符合用户角色权限的路由表
 * @param router
 * @param menus
 */
function filterRouterByMenus (router, menus, parentName) {
  if(menus && menus.length > 0) {
    for (let menu of menus) {
        let childrenRouter=[];
      if(menu.children && menu.children.length > 0) {
          childrenRouter = filterRouterByMenus([], menu.children,menu.title);
      }
        let newparam = menu.url.indexOf('.htm') >=0 ? menu.url : ''
        let newCpmponent = menu.url.indexOf('.htm') >=0 ? '/common' : menu.url
        let arr = menu.url.split('.htm')
        menu.url = menu.url.indexOf('.htm') >=0 ? arr[0] : menu.url
        if(newCpmponent == ''){
          continue;
        }
        //子菜单title特殊处理
        if (parentName&&parentName.length>0){
          // menu.title=parentName+"-"+menu.title;
          menu.title=menu.title;
        }
        let item = {
            path: menu.url,
            name: menu.title,
            meta: {
                icon: menu.icon,
                hidden: menu.hidden,
                param: newparam,
                optRight: menu.optRight
            },
            children:childrenRouter,
            component: childrenRouter.length>0?Module : resolve => require(['@/views/bizViews'+ newCpmponent + '.vue'], resolve),
            redirect: menu.redirect,
        };

        router.push(item)
    }
  }
  return router
}


