<template>
  <div class="h-topbar">
    <div class="h-topbar-logo" ref="topbarLogo">
      <!--<span class="h-topbar-logo-img"></span>-->
        <img src="../../assets/logo.png"/>
      <!--<span class="h-topbar-logo-span">{{sysName}}</span>-->
    </div>
    <div class="h-topbar-nav" ref="topbarNav">
      <div style="overflow: hidden; text-align:left;">
        <h-menu theme="dark" :active-name="activeIndex"  mode="horizontal" class="h-topbar-navMenu">
          <h-menu-item :name='index' @click.native.prevent.stop = "activeSidebar(index)"  v-for="(item, index) in menusRoot" :key="item.name"
          >{{item.title}}</h-menu-item>
        </h-menu>
      </div>
      <h-dropdown class="h-menu-more" v-if="showMore">
        <h-icon name="unfold"></h-icon>
        <h-dropdown-menu slot="list">
            <h-dropdown-item v-for="(item, index) in hideNavList" @click.native.prevent.stop = "activeSidebar(index)" :key="item.name">{{item.title}}</h-dropdown-item>
        </h-dropdown-menu>
      </h-dropdown>
    </div>


    <div class="h-topbar-tool" ref="topbarTool">
      <h-menu theme="dark" mode="horizontal">
          <h-menu-item name="systemdate">
              <span>系统日期：{{parseDate(sysDate)}}</span>
          </h-menu-item>
        <h-menu-item name="locktime" style="z-index: 10">
          <lock-screen :lockTime='locktime'></lock-screen>
        </h-menu-item>
        <h-submenu name='5' class="toolLast">
          <template slot='title'>
            <h-icon name="mine"></h-icon>
            <span class>{{userName}}</span>
          </template>
          <!-- <h-menu-item name='5-2' @click.native="handleResetPass">
            <h-icon name="editor"></h-icon>
            <span>修改密码</span>
          </h-menu-item> -->
          <!-- <h-menu-item name='5-3' @click.native="logout">
            <h-icon name="delete"></h-icon>
            <span>退出登录</span>
          </h-menu-item> -->

           <h-menu-item name="3-1">
              <div class="themes">
                <span class="label">系统换肤: </span>
                <div class="out-circle default" @click="handleChangeTheme('default')">
                  <div class="in-circle"></div>
                </div>
                <div class="out-circle dark" @click="handleChangeTheme('black')">
                  <div class="in-circle"></div>
                </div>
              </div>
            </h-menu-item>
          <h-menu-item name='5-2' @click.native="handleResetPass">
            <h-row>
              <h-icon name="editor"></h-icon>
              <span>修改密码</span>
            </h-row>
          </h-menu-item>
            <h-menu-item name="3-2">
              <h-row @click.native="logout">
                <i class="iconfont icon-android-close"></i>
                <span>退出登录</span>
              </h-row>
            </h-menu-item>
          </h-submenu>
      </h-menu>
    </div>
    <h-msg-box v-model="setPassword"
              title="重置密码"
              :mask-closable="false"
              style="text-align:left">
      <h-form ref="setPassForm" :model="setPassForm"  :label-width="80">
        <h-form-item prop="oldPassword" label="当前密码" required>
          <h-input type="password" v-model="setPassForm.oldPassword" placeholder="请输入当前密码" v-attrdel:title></h-input>
        </h-form-item>
        <h-form-item prop="newPassword" label="新密码" required :rules="validNewRegxPassCheck">
          <h-input type="password" v-model="setPassForm.newPassword" placeholder="请输入新密码" v-attrdel:title v-on:input="onInput"></h-input>
        </h-form-item>
        <h-form-item prop="newPasswordCheck" label="新密码确认" required :rules="validNewPassCheck">
          <h-input type="password" v-model="setPassForm.newPasswordCheck" placeholder="请再次输入新密码" v-attrdel:title></h-input>
        </h-form-item>
        <h-form-item  label="密码强度" class='input_span' >
         <span ref="one" class="one"></span>
         <span ref="two" class="two"></span>
         <span ref="three" class="three"></span>
        </h-form-item >
      </h-form>
      <div slot="footer">
        <h-button @click="setPassword = false">取消</h-button>
        <h-button type="primary" @click="handleSetPassword">确认</h-button>
      </div>
    </h-msg-box>
  </div>
</template>
<script>
  import { mapGetters, mapMutations } from 'vuex'
  import LockScreen from '@/components/LockScreen'
  import { changePwd,initMsg } from '@/api/login'
  import { allToggleClass } from '@/utils'
  import {
      parseDateStr,
      parseStrDate,
  } from '@/api/utils'
  import Cookies from 'js-cookie'
  import { saveSkin } from '@/api/utils'
  export function checkStrong(sValue) {
    let modes = 0;
    //正则表达式验证符合要求的
    if(sValue.length < 1) return modes;
    if(/\d/.test(sValue)) modes++; //数字
    if(/[a-z]/.test(sValue)) modes++; //小写
    if(/[A-Z]/.test(sValue)) modes++; //大写
    if(/\W/.test(sValue)) modes++; //特殊字符

    //逻辑处理
    switch(modes) {
      case 1:
        return 1;
        break;
      case 2:
        return 2;
        break;
      case 3:
      case 4:
        return sValue.length < 10 ? 3 : 4
        break;
    }
    return modes;
  }

  export default {
    data () {
      // 新密码框校验
      const validateRegxCheck = (rule, value, callback) => {
        let contentPattern = /^(?=.*\d)(?=.*[a-zA-Z])(?=.*[!@#$%^&*()])[\da-zA-Z!@#$%^&*()]{8,32}$/;
        if (value === '') {
          callback(new Error('请输入您的新密码'));
        } else if ( !contentPattern.test(value) ) {
          callback(new Error('密码请包含数字，字母，特殊字符，且长度至少8位'));
        }else {
          callback();
        }
      }
      // 确认密码框校验
      const validatePassCheck = (rule, value, callback) => {
        if (value === '') {
            callback(new Error('请再次输入您的新密码'));
        } else if (value !== this.setPassForm.newPassword) {
            callback(new Error('两次密码输入不一致'));
        } else {
            callback();
        }
      }
      return {
        setPassForm: {
          oldPassword: '',
          newPassword: '',
          newPasswordCheck: ''
        },
        validNewPassCheck: {
          validator: validatePassCheck, trigger: 'blur'
        },
        validNewRegxPassCheck: {
          validator: validateRegxCheck, trigger: 'blur'
        },
        setPassword: false,
        sysDate:Cookies.get('CurrSysDate'),
        userName:Cookies.get('OperatorName'),
        submitLoading: true,
        locktime: this.LOCAL_CONFIG.LOCK_TIME,
        theme: this.LOCAL_CONFIG.THEME,
        sysName: this.LOCAL_CONFIG.SYS_NAME,
        appname: '',
        canPage:true,
        rivalComplete:false,
        appList: [],
        allAppList: [],
        navWidth: 0,
        showMore: false,
        hideNavList: [],
        msgText: '',
        pwd: ''
      }
    },
    components: {
      LockScreen
    },
    computed: {
      ...mapGetters([
        'permission_routers',
        'menusRoot',
        'activeIndex'
      ]),
      hideNavListLength () {
        return this.hideNavList.length
      }
    },
    watch: {
      theme () {
        // allToggleClass(document.getElementsByTagName('html')[0], this.theme)
      },
      setPassword (val) {
        if (!val) {
          this.$refs.setPassForm.resetFields()
        }
      },
      hideNavListLength (val) {
        if (val > 0) {
          this.showMore = true
        } else {
          this.showMore = false
        }
      }
    },
    methods: {
      onInput(){
         this.msgText = checkStrong(this.setPassForm.newPassword);
         if(this.msgText > 1 || this.msgText == 1) {
           this.$refs.one.style.background='red'
         } else {
           this.$refs.one.style.background='#eee'
         }
         if(this.msgText > 2 || this.msgText == 2) {
           this.$refs.two.style.background='orange'
         } else {
           this.$refs.two.style.background='#eee'
         }
         if(this.msgText >= 3) {
           this.$refs.three.style.background='#00D1B2'
         } else {
           this.$refs.three.style.background='#eee'
         }
       },
        parseDate(dateInt){
          if (dateInt) {
            return parseDateStr(parseStrDate(dateInt),"yyyy年MM月dd日")
          } else {
            return '--'
          }
        },
        // 换肤
      handleChangeTheme (themeName) {
        this.$store.commit('SET_THEME', themeName)
        //保存数据库
        saveSkin(themeName);
      },
      handleResetPass () {
        this.handleResetForm('setPassForm')
        this.setPassword = true
        this.onInput()
      },
      handleResetForm (name) {
        this.$refs[name].resetFields();
      },
      handleSetPassword () {
          console.log("there")
        this.$refs['setPassForm'].validate((valid) => {
          if (valid) {
            changePwd( this.setPassForm.oldPassword, this.setPassForm.newPassword).then(res => {
              // 服务端基础服务出错，实际已修改成功
              if (res.data.success && res.data.data == "success") {
                  this.setPassword = false
                  this.$alert('重置密码成功!','成功',{
                      confirmButtonText: '确定',
                      type: 'success'
                  });
              } else {
                  this.$alert('重置密码失败：' + res.data.data,'失败',{
                      confirmButtonText: '确定',
                      type: 'error'
                  });
              }
            }).catch(error => {
                this.$alert('网络通信失败,服务器异常','提示',{
                    confirmButtonText: '确定',
                    type: 'info'
                });
            })
          } else{
              this.$alert('请正确填写密码!','错误',{
                  confirmButtonText: '确定',
                  type: 'error'
              });
          }
        })
      },
      logout () {
        this.$store.dispatch('Logout').then(() => {
          location.reload()
        })
      },
      activeSidebar (index) {
        if (index >= 0) {
          this.$store.dispatch('ActiveRootIndex', index)
        }
        // let item = this.menusRoot[this.activeIndex]
        // 防止页面刷新时会跳转根页面
        // if(this.$route.matched[0].path !== item.url) {
        //   this.$router.push({path: item.url})
        // }
      },
      change (val) {
      },
      scroll(num){
        let _this = this;
        if (num<=1 && _this.canPage) {
          _this.canPage = false;
          let length = _this.appList.length;
          setTimeout(() => {
            let arr = _this.allAppList.slice(length,length+10);
            if (arr.length>0) {
              _this.appList=_this.appList.concat(arr);
              _this.canPage = true;
            }else{
              this.isComputed = true
            }
          }, 200);
        }
      },
      computedNavWidth () {
        this.hideNavList = []
        if (this.$refs.topbarNav) {
          let logoWidth = this.$refs.topbarLogo ? this.$refs.topbarLogo.clientWidth : 0
          let toolWidth = this.$refs.topbarTool ? this.$refs.topbarTool.clientWidth : 0
          let searchWidth = this.$refs.topbarSearch ? this.$refs.topbarSearch.clientWidth : 0
          this.navWidth = document.getElementsByClassName('h-topbar')[0].clientWidth - logoWidth - toolWidth - searchWidth - 20
          let that = this
          this.$nextTick(function (){
            // 对nav menu子元素从后往前遍历，将超出部分保存至hideNavList
            // menu总长度 that.$refs.topbarNav.children[0].children.length === that.menusRoot.length
            let menuRootWidth = document.getElementsByClassName('h-topbar-navMenu')[0].clientWidth
            // menu子元素
            let menuChildren =  document.getElementsByClassName('h-topbar-navMenu')[0].children
            if (menuChildren && menuChildren.length > 0 && that.navWidth < menuRootWidth) {
              for (let i = menuChildren.length -1; i >= 0; i--) {
                if (that.navWidth < menuRootWidth) {
                  that.hideNavList.splice(0, 0, that.menusRoot[i])
                } else {
                  break
                }
                menuRootWidth = menuRootWidth - menuChildren[i].clientWidth
              }
            }
          })
        }
      }
    },
    created () {
      // 输入其他根目录下的路径时，切换sidebar
      if (this.menusRoot && this.menusRoot.length > 0) {
         if (this.$route.path.indexOf(this.menusRoot[this.activeIndex].url) < 0) {
          let urlArr = this.$route.path.split('/')
          let curUrl ='/' + urlArr[1]
          for (let [key, value] of this.menusRoot.entries()) {
            if (curUrl === value.url) {
              this.activeSidebar(key)
              return
            }
          }
        } else {
          // 更新对应的sidebar
          this.activeSidebar(this.activeIndex)
        }
      }

      //获取内部消息
      //initMsg();
      //setInterval(initMsg, 1000 * 60 * 5);// 5分钟轮询一次获取内部消息
    },
    mounted () {
      // document.getElementsByTagName('html')[0].className = this.theme;
      this.computedNavWidth()
      document.addEventListener('resize', this.computedNavWidth())
    }
  }
</script>

<style lang="less" rel="stylesheet/less">
  @import "~@/style/less/variable.less";
  .h-topbar {
    .themes {
      display: flex;
      flex-direction: row;
      .label {
        margin-right: 20px;
      }
      .out-circle {
        display: flex;
        justify-content: center;
        align-items: center;
        width: 20px;
        height: 20px;
        margin-right: 10px;
        border-radius: 10px;
        background-color: white;
        .in-circle {
          width: 16px;
          height: 16px;
          border-radius: 8px;
        }
      }
      .default {
        border: 2px solid @colorGray;
        background: @colorBlue;
      }
      .dark {
        border: 2px solid @colorGray;
        background: @colorBlack;
      }
    }
  }
  .input_span span {
    display: inline-block;
    width: 30%;
    height: 10px;
    background: #eee;
    line-height: 10px;
  }

  .one {
    border-top-left-radius: 5px;
    border-bottom-left-radius: 5px;
    border-right: 0px solid;
    margin-left: 20px;
    margin-right: 3px;
  }

  .two {
    border-left: 0px solid;
    border-right: 0px solid;
    margin-left: -5px;
    margin-right: 3px;
  }

  .three {
    border-top-right-radius: 5px;
    border-bottom-right-radius: 5px;
    border-left: 0px solid;
    margin-left: -5px;
  }

</style>


