var plugin = function (Vue) {
  return Vue.directive('themed', {
    bind: function (el, binding, vnode, oldVnode) {
      let type = binding.arg

      let changeStyles = (theme, type) => {
        if (type === 'text') {
          el.style.color = theme === 'black' ? 'white' : '#1F2834'
        }
        if (type === 'button') {
          let kinds = binding.modifiers
          if (kinds.primary) {
            el.style.color = 'white'
            el.style.border = '1px solid #0092FF'
          }
          if (kinds.ghost) {

          }
        }
      }

      changeStyles(store.state.theme.theme, type)
      SSObservable.subscribe('theme', (theme) => {
        changeStyles(theme, type)
      })
    },
    inserted: function (el) {
    }
  })
}

module.exports = plugin



