<template>
  <div class="page page-main">
    <div class="content-wrap">
      <!-- 左侧内容 -->
      <div class="content">
        <div class="region0">
          <div class="region-header">今日概况</div>
          <div class="region-content">
            <div class="card">
              <div>{{overview.totalCount}}</div>
              <div>指令总数</div>
            </div>
            <div class="card">
              <div style="cursor: pointer">
                <router-link to="/reportmgnt/transferRecordZt"
                             @click.native="addViews({name:'在途指令查询',path:'/reportmgnt/transferRecordZt'})">
                  {{overview.todoCount}}
                </router-link>
              </div>

              <div>待办指令数</div>
            </div>
            <div class="card">
              <div style="cursor: pointer">
                <div @click="addViewsParams({name:'完结指令查询',path:'/reportmgnt/transferRecordEnd',params:{ insStatus:'4,5'}})" class="mainindex-recordEnd">
                  {{overview.finishedCount}}
                </div>
              </div>
              <div>办结指令数</div>
            </div>
          </div>
        </div>
        <div class="region1">
          <div class="region-header">
            待办统计
          </div>
          <div class="region-content">
            <div class="card">
              <div style="cursor: pointer">
                <router-link to="/transfer/transferMake"
                             @click.native="addViews({name:'指令制作',path:'/transfer/transferMake'})">
                  {{overview.toSubmitCount}}
                </router-link>
              </div>
              <div>未提交</div>
            </div>
            <div class="card">
              <div style="cursor: pointer">
                <router-link to="/transfer/transferApprove"
                             @click.native="addViews({name:'指令审批',path:'/transfer/transferApprove'})">
                  {{overview.toApproveCount}}
                </router-link>
              </div>
              <div>未审批</div>
            </div>
            <div class="card">
              <div style="cursor: pointer">
                <router-link to="/transfer/transferSend"
                             @click.native="addViews({name:'指令发送',path:'/transfer/transferSend'})">
                  {{overview.toSendCount}}
                </router-link>
              </div>
              <div>未发送</div>
            </div>
            <div class="card">
              <div style="cursor: pointer">
                <router-link to="/transfer/transferSettle"
                             @click.native="addViews({name:'指令交收',path:'/transfer/transferSettle'})">
                  {{overview.toSettleCount}}
                </router-link>
              </div>
              <div>未交收</div>
            </div>
            <div class="card">
              <div style="cursor: pointer">
                <router-link style="color: #ef0909;" to="/transfer/transferSettle"
                             @click.native="addViews({name:'指令交收',path:'/transfer/transferSettle'})">
                  {{overview.transferFailedCount}}
                </router-link>
              </div>
              <div>划款失败</div>
            </div>
          </div>
        </div>
        <div class="region2">
          <div class="region-header">提醒</div>
          <div class="region-content">
            <div class="card">
              <div style="cursor: pointer">
                <router-link to="/fundaccount/fundaccount"
                             @click.native="addViews({name:'产品账户管理',path:'/fundaccount/fundaccount'})">
                  {{overview.newFundCount}}
                </router-link>
              </div>
              <div>今日产品新增</div>
            </div>
            <div class="card">
              <div style="cursor: pointer">
                <router-link to="/fundaccount/review"
                             @click.native="addViews({name:'产品账户复核',path:'/fundaccount/review'})">
                  {{overview.fundToCheckCount}}
                </router-link>
              </div>
              <div>待复核产品</div>
            </div>
            <div class="card">
              <div style="cursor: pointer">
                <router-link to="/fundaccount/review"
                             @click.native="addViews({name:'产品账户复核',path:'/fundaccount/review'})">
                  {{overview.fundAcctToCheckCount}}
                </router-link>
              </div>
              <div>待复核产品账户</div>
            </div>
            <div class="card">
              <div style="cursor: pointer">
                <router-link to="/pubaccount/review"
                             @click.native="addViews({name:'公共账户复核',path:'/pubaccount/review'})">
                  {{overview.commonAcctToCheckCount}}
                </router-link>
              </div>
              <div>待复核公共账户</div>
            </div>
            <div class="card">
              <div style="cursor: pointer">
                <router-link to="/feemanage/feeruleset"
                             @click.native="addViews({name:'费用规则配置',path:'/feemanage/feeruleset'})">
                  {{overview.gzToImportCount}}
                </router-link>
              </div>
              <div>未导入估值表</div>
            </div>
          </div>
        </div>

        <!--&lt;!&ndash; 地步按钮区域 &ndash;&gt;-->
        <!--<div class="footer"></div>-->
        <!--<div class="date-button" @click="dailyInit">-->
          <!--<div class="date-button-inner">-->
            <!--<div>日初始化</div>-->
            <!--<div>{{initError}}</div>-->
          <!--</div>-->
        <!--</div>-->
      </div>

      <!-- 右侧列表 -->
      <div class="fund-list">
        <div class="region-header">
          产品资金情况
        </div>
        <div class="region-content">

          <div class="list">
            <div class="list-content">
              <h-select style="margin-left: 0px" v-model="fundId" @on-change="fundChanged" filterable :clearable="false">
                <h-option v-for="item in fundList" :key="item.code" :label="item.text" :value="item.code">
                </h-option>
              </h-select>
              <div class="row">
                <div class="name">托管户余额</div>
                <div class="money">{{formatMoney(fundBalance)}}</div>
              </div>
            </div>
            <el-table :data="fundCashList" border show-summary size="small" :summary-method="getSummaries"
              style="width: 100%">
              <el-table-column prop="CASHTYPE" label="业务类型" width="120" align="left">
              </el-table-column>
              <el-table-column prop="AMOUNT" label="资金敞口" :formatter="formatter" align="right">
              </el-table-column>
            </el-table>
          </div>
        </div>
      </div>

    </div>
  </div>
</template>

<script type="text/ecmascript-6">
  import {
    mapGetters
  } from 'vuex'
  import {
    operatorList,
    getDictList,
    getFundInfo,
    getFundDetail,
    getBankList,
    getBrokerList,
    getCityTree,
    getRivalList,
    getIssuerList,
    getBusinFlagList,
    getBusinClassTree,
    parseDateString,
    parseDateStr,
    parseStrDate,
    smalltoBIG,
    resolveMacro,
    getSysParamValue,
    formatMoney,
    parseMinute,
    showCity,
    showText,
    getFundName,
    dailyInit
  } from '@/api/utils'
  import {
    findAccAmountByFundId,
    findFundBusinCash,
    getOverview,

  } from '@/api/fundAccount/fundAccount'
  import Cookies from 'js-cookie'

  export default {
    data() {
      return {
        curFundText: '',
        fundId: '',
        initError: '',
        fundBalance:0,
        fundList: [],
        fundCashList: [],
        overview: {
          commonAcctToCheckCount: 0,
          finishedCount: 0,
          fundAcctToCheckCount: 0,
          fundToCheckCount: 0,
          gzToImportCount: 0,
          newFundCount: 0,
          toApproveCount: 0,
          toSendCount: 0,
          toSettleCount: 0,
          toSubmitCount: 0,
          todoCount: 0,
          totalCount: 0,
          transferFailedCount: 0,
        },
      }
    },
    computed: {
      ...mapGetters([
        'sidebar'
      ]),
      isCollapse() {
        return !this.sidebar.opened
      },
      wrapStyles() {
          let styles = {}
          if (window.LOCAL_CONFIG.EMBEDDED) {
              styles = {
                  left: 0,
                  top: 0
              }
          } else {
              styles =  {
                  left: this.isCollapse ? '40px' : '200px',
                  top: '80px'
              }
          }
          console.log('styles =====', styles)
          return styles
      }
    },
    watch: {
      curFundText: {
        handler: function (val) {
          if (val) {
            let find = this.fundList.find(e => val.indexOf(e.text) >= 0)
            if (find) {
              this.fundId = find.code
              this.fundChanged()
            }
          }
        }
      }
    },
    created() {

    },
    activated() {
      this.refreshOverview()
      getFundInfo('/common/fundName.json', { transfer_flag: 1, fund_status: [1] }).then(res => {
        this.fundList = res.data
        if (this.fundList.length > 0) {
          this.curFundText = this.fundList[0].text
        }
      });
      this.fundChanged();
    },
    methods: {
      addViews (item) {
          this.$store.dispatch('addVisitedViews', item)
      },
      addViewsParams(item) {
        this.$router.push({path: item.path, name: item.name, params: item.params});
        this.$store.dispatch('addVisitedViews', item);
      },
      formatter(row, column, cellValue, index) {
        return formatMoney(cellValue)
      },
      formatMoney: formatMoney,
      refreshOverview() {
        getOverview().then(res => {
          if (res.data.success)
            this.overview = res.data.data
        })
      },
      fundChanged() {
        findFundBusinCash(this.fundId, Cookies.get('CurrSysDate')).then(res => {
          this.fundCashList = res.data;
        });
        //获取账户余额
        findAccAmountByFundId(this.fundId, Cookies.get('CurrSysDate'), null).then(res => {
          if (res && res.data && res.status === 200) {
            if (res.data && res.data.length > 0) {
              this.fundBalance = res.data[0].ACCOUNT_CUR_BALANCE;
            } else {
              this.fundBalance = 0;
            }
          }
        });
      },
      getSummaries(param) {
        const {
          columns,
          data
        } = param;
        const sums = [];
        columns.forEach((column, index) => {
          if (index === 0) {
            sums[index] = '总计';
            return;
          }
          const values = data.map(item => Number(item[column.property]));
          if (!values.every(value => isNaN(value))) {
            sums[index] = values.reduce((prev, curr) => {
              const value = Number(curr);
              if (!isNaN(value)) {
                return prev + curr;
              } else {
                return prev;
              }
            }, 0);
            sums[index] = formatMoney(sums[index])
            // sums[index] += ' 元';
          } else {
            sums[index] = 'N/A';
          }
        });

        return sums;
      },
      dailyInit(){
        this.initError= ''
        dailyInit().then(res=>{
          if (res.data.success){
            Cookies.set('CurrSysDate', res.data.data);
            location.reload();
          }
          this.initError= res.data.msg
        })
      },
    },
    components: {

    }
  }

</script>

<style lang="less" rel="stylesheet/less">
  @import "~@/style/less/mixins.less";

  .page-main {
    width: 100%;
    height: 100%;
    .content-wrap {
      .absolute(0, 0, 0, 0);
      min-width: 800px;
      background-color: #F7FAFB;
      border: 1px solid #F7FAFB;
      // 左侧内容
      .content {
        .absolute(0, 320px, 0, 0);
        padding: 20px 20px 20px 20px;
        overflow: hidden;
        .region0, .region1, .region2  {
          background-color: white;
          border: 1px solid #e3ecf3;
        }
        .region0, .region1, .region2 {
          width: calc(~"100% - 10px");
          height: calc(~"33.3% - 6.5px");
        }
        .region1, .region2 {
          margin-top: 10px;
        }
        .region0 .card {
          width: 33%;
        }
        .region1 .card {
          width: 20%;
        }
        .region2 .card {
          width: 25%;
        }
        .region-header {
          .seperator();
          .flex(row, flex-start, center);
          position: relative;
          height: 50px;
          padding-left: 20px;
          font-weight: bold;
          font-size: 14px;
          color: #333333;
        }
        .card {
          .flex(column, center, center);
          & > div:first-child {
            font-size: 54px;
            line-height: 54px;
            font-weight: normal;
            color: #0178e1;
          }
          & > div:last-child {
            font-size: 16px;
            color: #333333;
          }

          .mainindex-recordEnd:hover {
            color: #8fc3ff;
          }
        }
        .region-content {
          display: flex;
          justify-content: center;
          height: calc(~"100% - 50px");
        }
      }

      // 右侧列表
      .fund-list {
        .absolute(auto, 20px, 20px, 20px);
        width: 320px;
        background-color: white;
        border: 1px solid #e3ecf3;
        overflow: auto;
        .region-header {
          .seperator();
          .flex(row, flex-start, center);
          position: relative;
          height: 50px;
          padding-left: 20px;
          font-weight: bold;
          font-size: 14px;
          color: #333333;
          .dropdown-title {
            display: flex;
            justify-content: center;
            align-items: center;
            cursor: pointer;
            & > span {
              font-size: 14px;
              color: #333333;
            }
            .icon-android-arrow-dropdo {
              font-size: 24px;
              color: #50AEEC;
            }
          }
        }
        .region-content {
          justify-content: flex-start;
          overflow: auto;
          .list {
            width: 100%;
            .list-header {
              .flex(row, flex-start, center)
            }
            .list-content {
              position: relative;
              .row {
                .seperator();
                position: relative;
                display: flex;
                width: 100%;
                height: 36px;
                line-height: 36px;
                padding: 0 6px;
                .name {
                  color: #333333;
                  flex-grow: 1;
                }
                .money {
                  color: #666666;
                }
              }
              & > .row:nth-child(2n) {
                background-color: #f8f8f8;
              }
            }
          }
        }
      }
    }
  }

</style>



