import Url from 'url'

function find (list, f) {
  return list.filter(f)[0]
}

function deepCopy (obj, cache) {
  if (cache === void 0) cache = []

  // just return if obj is immutable value
  if (obj === null || typeof obj !== 'object') {
    return obj
  }

  // if obj is hit, it is in circular structure
  var hit = find(cache, function (c) { return c.original === obj })
  if (hit) {
    return hit.copy
  }

  var copy = Array.isArray(obj) ? [] : {}
  // put the copy into cache at first
  // because we want to refer it in recursive deepCopy
  cache.push({
    original: obj,
    copy: copy
  })

  Object.keys(obj).forEach(function (key) {
    copy[key] = deepCopy(obj[key], cache)
  })

  return copy
}


function parseUrl(url) {
  let result = ''

  let hasHash = (string) => {
    return string.indexOf('#') >= 0
  }
  let hasQuery = (string) => {
    return string.indexOf('?') >= 0
  }
  if (hasHash(url)) {
    let arr = url.split('#')
    let subUrl1 = arr[0]
    let subUrl2 = arr[1]
    let subUrlObj1 = null
    let subUrlObj2 = null

    if (hasQuery(subUrl1)) {
      subUrlObj1 = Url.parse(subUrl1, false)
    }
    if (hasQuery(subUrl2)) {
      subUrlObj2 = Url.parse(subUrl2, false)
    }
    if (!(subUrlObj1 && subUrlObj2)) {
      result = subUrl1 + '#' + subUrl2
    }
    if (subUrlObj1 && !subUrlObj2) {
      let { protocol, host, path } = subUrlObj1
      result = protocol + '//' + host + path + '#' + subUrl2
    }
    if (!subUrlObj1 && subUrlObj2) {
      let { query, pathname } = subUrlObj2
      result = subUrl1 + '?' + query + '#' + pathname
    }
  } else {
    result = url
  }
  return result
}

function uniqueArray(array, key) {
  let result = []
  for (let i = 0; i < array.length; i++) {
    if (result.findIndex(e => e[key] === array[i][key]) < 0) {
      result.push(array[i])
    }
  }
  return result
}

export { deepCopy, parseUrl, uniqueArray }



