function formatNumber(num, precision, separator) {
  let parts = ''
  // 判断是否为数字
  if (!isNaN(parseFloat(num)) && isFinite(num)) {
    num = Number(num)
    // 处理小数点位数
    num = (typeof precision !== 'undefined' ? num.toFixed(precision) : num).toString()
    // 分离数字的小数部分和整数部分
    parts = num.split('.')
    // 整数部分加[separator]分隔, 借用一个著名的正则表达式
    parts[0] = parts[0].toString().replace(/(\d)(?=(\d{3})+(?!\d))/g, '$1' + (separator || ','))
    return parts.join('.')
  }
  return null
}

export function formatMoney(num) {
  if (!(typeof (num) === 'number' || typeof (num) === 'string')) {
    return num
  }
  if (typeof (num) === 'string') {
    num = parseFloat(num)
  }
  return formatNumber(num, 2, ',')
}

export function nodeNameDesc(nodeName) {
  let typeName = ''
  switch (nodeName) {
  case 'approve':
    typeName = '经办'
    break
  case 'settle':
    typeName = '交收'
    break
  default:
    typeName = nodeName
    break
  }
  return typeName
}

export function settlementDesc(code) {
  let list = [
    {
      code: '1',
      text: '见款付券'
    },
    {
      code: '2',
      text: '见券付款'
    },
    {
      code: '3',
      text: 'DVP'
    }]
  let res = list.find(e => e.code === code + '')
  if (res) {
    return res.text
  }
  return code
}

