import fetch from '../httpFetch.js'

/**
 * post方式发送请求方法
 * @param {*参数} params
 * @param {*请求url} url
 */
export function post(params,url) {
    return fetch.post(url, params)
}

/**
 * get方式发送请求方法
 * @param {*参数} params
 * @param {*请求url} url
 */
export function get(params,url) {
    return fetch.get(url, {params})
}

export function getfundinfo() {
    return fetch.post('/base/cache/fundinfoGet',{});
}

export function Getcombi() {
    return fetch.post('/base/cache/combiGet',{});
}

export function Getoperator() {
    return fetch.post('/base/cache/operatorGet',{});
}

export function getDicts(dicNo) {
    return fetch.post('/base/cache/getDict',{dic_no:dicNo});
}

export function Getstockinfo() {
    return fetch.post('/base/cache/stockinfoGet',{});
}

export function GetCombibyoperator() {
    return fetch.post('/base/cache/combigetbyoperator',{});
}

export function dateToInt(sDate){ //yyyy-MM-dd格式转换为8位整数
  var sTmpDate= sDate.replace(/-/g, "");
  return parseInt(sTmpDate,10);
}

export function intToDate(intDate){ //8位整数转化为yyyy-MM-dd格式
  var sDate,sYear,sMonth,sDay;
  sDate = intDate.toString();
  sYear = sDate.substr(0,4);
  sMonth = sDate.substr(4,2);
  sDay = sDate.substr(6,2);
  sDate = sYear + '-' + sMonth + '-' + sDay;
  return sDate;
}

export function ShowText(key, keyValueList, attributes) {
  for (var i = 0; i < keyValueList.length; i++) {
    var row = keyValueList[i];
    if (key == row[attributes.key]) {
      return row[attributes.value]
    }
  }
}

export function showTab(name) {
    let src = this.tabList[name].menu_path;
    if (!document.getElementById("frame" + name).getAttribute("src")) {
        document.getElementById("frame" + name).setAttribute("src", src);
    }
}


