var numberFormatter = require('@utils/number').default

var plugin = function (Vue) {
  return Vue.directive('colored', {
    update: function (el) {
      let val = numberFormatter.parseNumber(el.innerHTML || '0')
      if (parseFloat(val) > 0) {
        el.style.color = 'red'
      }
      if (parseFloat(val) < 0) {
        el.style.color = 'green'
      }
    }
  })
}

module.exports = plugin



