<template>
  <div class="s-context-menu" :style="{left: pos.x + 'px', top: pos.y + 'px'}" v-clickoutside:mousedown="remove" v-if="visible">
    <div class="s-context-menu-item" v-for="(menu, idx) in menus" :key="idx" @click="onClickMenu(idx, menu)">{{menu}}</div>
  </div>
</template>

<script type="text/ecmascript-6">
import Clickoutside from '@/directive/clickoutside'

export default {
  props: {
    pos: {
      type: Object,
      default: () => { return { x: 0, y: 0 } }
    },
    menus: {
      type: Array,
      default: () => []
    },
    onMenuClick: {
      type: Function,
      default: null
    }
  },
  data() {
    return {
      visible: false
    }
  },
  created() {

  },
  methods: {
    onClickMenu(index, menuTitle) {
      this.onMenuClick && this.onMenuClick(index, menuTitle)
    }
  },
  directives: {
    Clickoutside
  }
}
</script>

<style lang="less" rel="stylesheet/less">
.s-context-menu {
  position: absolute;
  overflow: hidden;
  left: 0;
  top: 0;
  border-radius: 4px;
  background-color: white;
  box-shadow: 0 1px 6px rgba(0, 0, 0, 0.2);
  z-index: 1000;
  &-item {
    min-width: 80px;
    height: 32px;
    line-height: 32px;
    padding: 0 6px;
    font-size: 13px;
    color: #666f89;
  }
  &-item:hover {
    color: white;
    background-color: #4a94fa;
    cursor: pointer;
  }
}
</style>



