<template>
  <div class="cmp-tabsview">
    <div class="content">
      <div class="menu-card"
          v-for="(view, idx) in visitedViews"
          :key="idx"
          :class="{active: activeMenuName === view.name}"
          @click.right.stop.prevent="showContextMenu($event, idx)">
        <span class="menu-name" @click="navigateTo(view)">{{view.name}}</span>
        <i class="iconfont icon-close" @click="closeMenu(idx)" v-if="idx !== 0"></i>
      </div>
    </div>
  </div>
</template>

<script type="text/ecmascript-6">
  import { mapState, mapGetters, mapActions } from 'vuex'
  import SContextMenu from '@/components/SContextMenu'

  export default {
    data() {
      return {
        activeMenuName: '我的工作台',
        limit: this.LOCAL_CONFIG.TABS_VIEW_LIMIT,
        menu: null
      }
    },
    computed: {
      ...mapState({
        visitedViews: state => state.app.visitedViews
      })
    },
    watch: {
      $route(to, from) {
        this.activeMenuName = to.name
      },
      visitedViews: {
        handler: function(val) {
        }
      }
    },
    mounted() {
      this.initTabsView()
    },
    methods: {
      ...mapActions({
        delCurVisitedViews: 'delCurVisitedViews',
        delAllVisitedViews: 'delAllVisitedViews',
        delOtherVisitedViews: 'delOtherVisitedViews'
      }),
      initTabsView() {
        let view = {
          name: this.$route.name,
          path: this.$route.path,
        }
        this.$store.dispatch('addVisitedViews', view)
        this.activeMenuName = view.name
        SSObservable.publish('tabviewChange', view.name)
      },
      navigateTo(view) {
        if (view) {
          this.activeMenuName = view.name
          this.$router.push(view.path).catch(e => {})
          SSObservable.publish('tabviewChange', view.name)
        }
      },
      showContextMenu(event, tabIndex) {
        event.preventDefault()
        this.menu = SContextMenu({
          pos: {
            x: event.clientX,
            y: event.clientY
          },
          menus: ['关闭当前选项卡', '关闭全部选项卡', '关闭当前以外选项卡'],
          onMenuClick: (index, menuTitle) => {
            this.menu.remove()
            if (index === 0) {
              this.closeMenu(tabIndex)
            } else if (index === 1) {
              this.closeAllMenus()
            } else {
              this.closeOthersExceptOne(tabIndex)
            }
          }
        })
        this.menu.show()
      },
      closeMenu(tabIndex) {
        if (tabIndex === 0) {
          return
        }
        let view = this.visitedViews[tabIndex]
        this.delCurVisitedViews(view)

        if (view.name === this.activeMenuName) {
          this.navigateTo(this.visitedViews[this.visitedViews.length - 1])
        }
      },
      closeAllMenus() {
        this.delAllVisitedViews()
        this.navigateTo(this.visitedViews[0])
      },
      closeOthersExceptOne(tabIndex) {
        let view = this.visitedViews[tabIndex]
        if (tabIndex === 0) {
          this.delAllVisitedViews()
        } else {
          this.delOtherVisitedViews(view)
        }
        this.navigateTo(view)
      }
    }
  }
</script>

<style lang="less" rel="stylesheet/less">
@import "~mixins";
.cmp-tabsview {
  position: relative;
  height: 40px;
  .content {
    .flex(row, flex-start, center);
    .seperator(bottom, 0, #e3ecf3);
    position: relative;
    height: 100%;
    background-color: white;
    // margin-right: 4px;
    .menu-card {
      .flex(row, flex-start, center);
      position: relative;
      box-sizing: border-box;
      height: 30px;
      margin-right: 10px;
      margin-top: 8px;
      padding-left: 10px;
      font-size: 12px;
      color: #999999;
      flex-shrink: 0;
      z-index: 1;
      .menu-name {
        margin-right: 10px;
        cursor: pointer;
      }
      .icon-close {
        width: 20px;
        text-align: center;
        color: #999999;
        cursor: pointer;
      }
    }
    & > .menu-card:first-child {
      margin-left: 4px;
    }
    .active {
      color: #333333;
      border-left: 1px solid #e3ecf3;
      border-top: 1px solid #e3ecf3;
      border-right: 1px solid #e3ecf3;
      background-color: #F7FAFB;
    }
  }
}
</style>



