import Vue from 'vue'
import MsgBox from './MsgBox.vue'

var BoxConstructor = Vue.extend(MsgBox)

function getInstance() {
  return new BoxConstructor({
    el: document.createElement('div')
  })
}

let showBox = function (content, title, options, resolve) {
  let instance = getInstance()
  document.body.appendChild(instance.$el)
  instance.visible = true
  instance.title = title || ''
  instance.content = content || ''
  if (options.type === 'success') {
    instance.duration = options.duration || 1500
  } else {
    instance.duration = 0
  }
  instance.multiline = options.multiline || false
  instance.type = options.type || 'info'
  instance.callback = options.callback || null
  instance.resolve = resolve
  instance.zIndex = options.zIndex || '1000'
  instance.tag = options.tag || ''
}

let _MsgBox = function (content, title, options) {
  return new Promise((resolve) => {
    showBox(content, title, options, resolve)
  })
}

export default _MsgBox



