/**
 * 主题相关
 */
import {
  SET_THEME
} from '../mutation-types'
import { replaceClass } from '@utils/dom'
import { saveSkin } from '@/api/utils'

let state = {
  theme: localStorage.getItem('theme') || 'default'
}

let getters = {
  getTheme: state => 's-theme-' + state.theme
}

let mutations = {
  [SET_THEME](state, themeName) {
    let names = ['white', 'default', 'black']
    if (names.findIndex(e => e === themeName) < 0) {
      return
    }
    // default 即 white
    state.theme = (themeName === 'white' ? 'default' : themeName)
    localStorage.setItem('theme', state.theme)
    replaceClass(document.getElementsByTagName('html')[0], 's-theme-' + state.theme)
    window.SSObservable.publish('theme', state.theme)
  }
}

export default {
  state,
  getters,
  mutations

}



