import {
  SET_SUBTAB_NAME,
  ADD_SUBTAB_NAME,
  DEL_SUBTAB_NAME,
  SET_SUBTAB_NAME_DICT
} from '../mutation-types'

let state = {
  subTabName: localStorage.getItem('subTabName') || '',
  subTabNames: JSON.parse(localStorage.getItem('subTabNames') || '[]'),
  subTabNameDict: JSON.parse(localStorage.getItem('subTabNameDict') || '{}')
}

let mutations = {
  [SET_SUBTAB_NAME](state, subTabName) {
    state.subTabName = subTabName
    localStorage.setItem('subTabName', state.subTabName)
  },
  [ADD_SUBTAB_NAME](state, subTabName) {
    let stored = state.subTabNames
    if (stored.indexOf(subTabName) < 0) {
      stored.push(subTabName)
    }
    localStorage.setItem('subTabNames', JSON.stringify(stored))
  },
  [DEL_SUBTAB_NAME](state, subTabName) {
    let stored = state.subTabNames
    let find = stored.findIndex(e => e === subTabName)
    console.log('find', subTabName, find, stored)
    if (find >= 0) {
      stored.splice(find, 1)
    }
    localStorage.setItem('subTabNames', JSON.stringify(stored))
  },
  [SET_SUBTAB_NAME_DICT](state, data) {
    let rootpath = data.rootpath
    let subTabName = data.subTabName
    let stored = state.subTabNameDict
    stored[rootpath] = subTabName
    localStorage.setItem('subTabNameDict', JSON.stringify(stored))
  }

}

export default {
  state,
  mutations
}



