import fetch from "@/api/httpFetch";

export function saveFundInfo(fundInfo){
    return fetch.post('/fundaccount/saveFundInfo.json',fundInfo, {
        headers: {
            'Content-Type': 'application/json;charset=UTF-8'
        },
        transformRequest: function (req) {
            return JSON.stringify(req);
        }
    });
}
export function synFund() {
    return fetch.post('/fundaccount/synFund.json');
}
export function disableFund(fundId) {
    return fetch.post('/fundaccount/disableFund.json',{fundId:fundId});
}
export function deleteFund(fundId) {
    return fetch.post('/fundaccount/deleteFund.json',{fundId:fundId});
}

export function getBicCodeByCustodialNo(custodialNo) {
    return fetch.post('/common/getBicCodeByCustodialNo.json',{custodialNo: custodialNo});
}

export function saveAccount(account) {
    return fetch.post('/fundaccount/saveAccount.json',account, {
        headers: {
            'Content-Type': 'application/json;charset=UTF-8'
        },
        transformRequest: function (req) {
            return JSON.stringify(req);
        }
    });
}
export function deleteAccount(acctIdList,fundId) {
    return fetch.post('/fundaccount/deleteAccount.json',{fundId:fundId,accountIdList:acctIdList});
}

export function findAccAmountByFundId(fundId, currDate, bankAccId) {
    return fetch.post('/welcome/findAccAmountByFundId.json', {fundId: fundId, currDate: currDate, bankAccId: bankAccId});
}

export function findFundCashByFundId(fundId, currDate) {
    return fetch.post('/fundaccount/findFundCashByFundId.json', {fundId: fundId, currDate: currDate});
}
export function findFundBusinCash(fundId, currDate) {
  return fetch.post('/welcome/findFundBusinCash.json', {fundId: fundId, currDate: currDate});
}

export function getOverview() {
  return fetch.get('/welcome/getCurrentOverview.json', {});
}


export function pageFundList(pageNo,pageSize,fundType,fundTrustBank,keyword,trustType) {
    if (!trustType) {
        trustType = '';
    }
  return fetch.post('/fundaccount/page/fundInfo.json',{pageNo:pageNo,pageSize:pageSize,fundType:fundType,fundTrustBank:fundTrustBank,keyword:keyword,trustType:trustType})
}

export function getFundAccount(fundId) {
    return fetch.post('/fundaccount/getFundAccount.json',{fundId: fundId})
}

export function getFundAccountList(fundTrustBankList, fundTypeList, fundStatusList, dataSource, keyword) {
    return fetch.post('fundaccount/getFundAccountList.json', {
        fundTrustBankList: fundTrustBankList,
        fundTypeList: fundTypeList,
        fundStatusList: fundStatusList,
        dataSource: dataSource,
        keyword: keyword
    })
}



